@import '../../../../../../assets/mixins/colors.scss';

#Receiving {
  .ReceivingResults {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__qr {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 234px;
      height: 234px;
      margin-bottom: 32px;
      padding: 22px;
    }

    &__qr-currnecy {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &__merchant {
      margin-top: 8px;
      margin-bottom: 32px;
      font-size: 14px;
      line-height: 16px;
      color: $darkGray;        
    }

    &__merchant-name {
      font-weight: bold;
      color: $blue;
      text-decoration: none;
    }
    
    &__finish-button {
      min-width: 180px;
      text-transform: none;
    }
  }
}