@import '../../assets/mixins/colors';

.select {
  position: relative;
  width: 100%;
  cursor: pointer;

  label {
    position: absolute;
    top: -8px;
    left: 8px;
    padding: 0 4px;
    background: linear-gradient(transparent 50%, #F5F7FB 50%);
    color: rgba(156, 163, 166, 0.6);
    font-size: 12px;
    line-height: 16px;
    transition: all 0.3s ease-in-out;
  }
  span {
    display: block;
    padding: 12px;
    width: 100%;
    height: 40px;
    background: #F5F7FB;
    border: 1px solid #E6E6E6;
    border-radius: 4px;
    color: #464646;
    font-size: 14px;
    line-height: 16px;
    user-select: none;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
  }
  i {
    position: absolute;
    top: 12px;
    right: 8px;
  }
  ul {
    position: absolute;
    top: 46px;
    left: 0;
    width: 100%;
    background: #FFFFFF;
    border-radius: 0 0 4px 4px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    list-style-type: none;
    z-index: 10;
    max-height: 200px;
    overflow: auto;

    li {
      padding: 12px;
      color: #464646;
      font-size: 12px;
      line-height: 14px;
      border-bottom: 1px solid #EFEFEF;
      transition: all 0.3s ease-in-out;

      &.active, &:hover {
        background: rgba(13, 98, 181, 0.1);
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
}
