@import '../../../assets/mixins/colors';

.mainTabHistory {
  display: flex;
  flex-direction: column;
  width: 100%;

  .mainTabHistorySummary {
    display: flex;
    align-items: center;
    margin-bottom: 16px;

    .mainTabHistorySummaryItem {
      display: flex;
      flex-direction: column;
      padding: 8px;
      width: 100%;
      background: #FFFFFF;
      border: 1px dashed transparent;
      border-radius: 4px;

      &:last-child {
        border: 1px dashed #0455A3;
      }
      span {
        margin-bottom: 8px;
        color: #9FA3A6;
        font-size: 12px;
        line-height: 14px;
      }
      p {
        color: #464646;
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;
      }
    }
    .mainTabHistorySummarySeparator {
      display: flex;
      flex-direction: column;

      div {
        margin: 2px 3px;
        width: 14px;
        height: 2px;
        background: #9FA3A6;
        border-radius: 4px;
      }
    }
  }
}
