#RootModal {
  .RootModal__Container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 512px;
    max-height: 96%;
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
    border-radius: 8px;
  }

  &.close .RootModal__Container{
    animation: closeModal .3s !important
  }

  .Container__Header {
    position: relative;
    margin: 0 0 24px 0;
    padding: 56px 0 0 0;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;

    .MuiTypography-h2 {
      font-weight: 700;
      letter-spacing: 0;
    }

    &__Close {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate3d(-24px, 24px, 0);

      &:hover {
        cursor: pointer;
      }
    }
  }

  .Container__Body {
    padding: 20px 88px 0 88px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

}

@keyframes closeModal{
  from {
    transform: translate(-50%, -50%);
  }

  to {
    transform: translate(-50%, -50%) scale(0);
  }
}