.ConfirmationCommission {
  margin-bottom: 12px;
  padding: 12px 0 16px 0;
  border-bottom: 1px solid #EFEFEF;

  .ConfirmationCommission__Label {
    padding-bottom: 4px;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;

    &.additional {
      padding: 0 0 4px 0;
      font-size: 12px;
      color: #464646;
    }
  }

  .ConfirmationCommission__Labels {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-grow: 1;

    .label {
      margin-bottom: 4px;
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: #464646;

      &.speed {
        justify-content: flex-end;
      }

      .value {
        margin-right: 4px;
      }

      .currency {
        font-size: 10px;
        line-height: 12px;
        color: #9FA3A6;
        transform: translate3d(0, 1px, 0);
      }
    }
  }

  .ConfirmationCommission__Details {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-grow: 1;

    .detail {
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 16px;
      color: #9FA3A6;

      .currency {
        margin-right: 4px;
      }

      &.speed {
        justify-content: flex-end;
      }
    }
  }
}
