@import '../../assets/mixins/colors';

.merchant {
  display: flex;
  flex-direction: column;
  height: 100%;

  .merchantsModal {
    .modalContainerBody {
      form {
        width: 100%;

        .input {
          margin-bottom: 16px;
        }
        .merchantsModalIsReusable {
          margin-bottom: 16px;
          display: inline-block;

          label  span {
            margin-left: 4px;
            margin-right: 5px;
            opacity: 1;
          }

          .tooltip {
            transform: none;
          }
        }
      }
    }
  }
  .merchantHead {
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    .merchantHeader {
      display: flex;
      width: 296px;
      margin-right: 16px;

      .backButton {
        height: 24px;
        margin-right: 8px;

        i svg path {
          fill: $darkGray;
          stroke: none;
        }

        &:hover {
          i svg path {
            fill: $blue;
          }
        }
      }

      h2 {
        color: $black;
        font-size: 18px;
        font-weight: bold;
        line-height: 24px;
      }
    }

    .merchantActions {
      display: flex;
      justify-content: space-between;
      flex-grow: 1;
      height: 40px;

      .merchantHeadButtons {
        display: flex;
        align-items: center;
  
        button {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-left: 16px;
          padding: 0;
          width: 40px;
          height: 40px;
  
          &:first-child {
            margin-left: 0;
          }
          &.fundsHeadButtonsCheck {
            width: 240px;
          }
          &.fundsHeadButtonsSend,
          &.fundsHeadButtonsReceive {
            width: 130px;
          }
          &.fundsHeadButtonsAutoWithdrawal {
            width: 200px;
          }
          i {
            margin-right: 0;
          }
        }
      }
    }
  }

  .tableWrapper {
    height: calc(100% - 48px);
  }

  .tabContent {
    display: flex;
    align-items: flex-start;
    height: calc(100% - 48px);

    .merchantCurrencies {
      display: flex;
      flex-direction: column;
      margin-bottom: 32px;
  
      h4 {
        margin-bottom: 16px;
        color: #464646;
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;
      }
    }
    
    .tableWrapper {
      width: calc(100% - 312px);
      height: 100%;
      max-height: none;
  
      .merchantTablesMenu {
        display: flex;
        list-style-type: none;
        margin-bottom: 16px;
  
        li {
          margin-right: 32px;
          transition: all 0.3s ease-in-out;
          cursor: pointer;
  
          a {
            color: $darkGray;
            font-size: 14px;
            font-weight: bold;
            line-height: 16px;
            text-decoration: none;
  
            &:focus {
              opacity: 1;
            }
          }
  
          &:last-child {
            margin-right: 0;
          }
  
          &:hover a, a.active {
            color: $black;
          }
        }
      }
    }
  }
}
