@import 'breakpoints';

$SIZE : 1;
$R1920SIZE : 0.9;
$R1600SIZE : 0.8;
$R1366SIZE : 0.8;
$R1280SIZE : 0.75;
$R1024SIZE : 0.75;
$R768SIZE : 0.75;
$R576SIZE : 0.75;

:root {
  --size: #{$SIZE};
}

:root[responsive="true"] {
  --size: #{$SIZE};

  @include BPR1920 {
    --size: #{$R1920SIZE};
  }

  @include BPR1600 {
    --size: #{$R1600SIZE};
  }

  @include BPR1366 {
    --size: #{$R1366SIZE};
  }

  @include BPR1280 {
    --size: #{$R1280SIZE};
  }

  @include BPR1024 {
    --size: #{$R1024SIZE};
  }

  @include BPR768 {
    --size: #{$R768SIZE};
  }

  @include BPR576 {
    --size: #{$R576SIZE};
  }
}
