.TransactionError {
  width: 100%;
  height: auto;

  .TransactionError__Icon {
    margin-bottom: 6px;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .TransactionError__Title {
    margin-bottom: 18px;
    font-weight: bold;
    font-size: 26px;
    line-height: 32px;
    color: #464646;
    text-align: center;
  }

  .TransactionError__Label {
    margin-bottom: 6px;
    font-size: 18px;
    line-height: 24px;
    color: #9FA3A6;
    text-align: center;
  }
}

#RootModal .RootModal__Container {
  &#Withdrawal.withdrawal-error {
    height: 344px;
    animation-name: slideModal;
    animation-duration: .3s;

    .Container__Header {
      border: none;
    }

    .Container__Body {
      padding: 0 56px 0 56px;
      overflow: hidden;
    }
  }
}

@keyframes slideModal {
  from {
    transform: scale(.1) translate(-100%, -100%);
    width: 0;
    height: 0;
  }

  to {
    transform: scale(1) translate(-50%, -50%);
    width: 512px;
    height: 344px;
  }
}