@import '../../assets/mixins/colors';

.notifications {
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 330px;
  transform: translateX(-50%);
  z-index: 10000;

  &.notificationsAuth {
    top: 56px;
  }

  &.white {
    .notification {
      &.notificationSuccess {
        background: rgba(255, 255, 255, 0.9);
      }
    }
  }

  .notification {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 8px;
    padding: 16px;
    width: 100%;
    border-radius: 6px;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    text-transform: uppercase;

    &.notificationError {
      background: rgba(255, 97, 97, 0.1);
      border: 1px solid #FF6161;
      color: #FF6161;

      .notificationClose svg path {
        fill: #FF6161;
      }
    }
    &.notificationSuccess {
      background: rgba(4, 85, 163, 0.1);
      border: 1px solid #0455A3;
      color: #0455A3;

      .notificationClose svg path {
        fill: #0455A3;
      }
    }
    &.opaque {
      background: white;
    }
    .notificationClose {
      position: absolute;
      top: 4px;
      right: 4px;
      cursor: pointer;
    }
  }
}
