.MerchantSelect {
  margin-bottom: 16px;
  padding: 0 12px;
  position: relative;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  background: #F5F7FB;
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  border-radius: 4px;

  &.disabled {

    & > * {
      opacity: 0.5;
    }

    .click-zone:hover {
      cursor: not-allowed;
    }
  }

  .click-zone {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: transparent;

    &:hover {
      cursor: pointer;
    }
  }

  .MerchantSelect__Title {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 4px;
    transform: translate3d(0, -9px, 0);
    background: linear-gradient(transparent 50%, #F5F7FB 50%);
    color: rgba(156, 163, 166, 0.6);
    font-size: 12px;
    line-height: 16px;
    transition: all 0.3s ease-in-out;
  }

  .MerchantSelect__Placeholder {
    width: 100%;
    height: auto;
  }

  .Placeholder {
    width: calc(100% - 18px);
    height: auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    line-height: 16px;
    color: #464646;
  }

  .MerchantSelect__Dropdown {
    position: absolute;
    width: 100%;
    height: auto;
    top: 0;
    left: 0;
    z-index: 30;
    background: #FFFFFF;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    .Dropdown__Search {
      padding: 16px 14px;
      width: 100%;
      height: 88px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #EFEFEF;
    }

    .Search__Title {
      margin-bottom: 8px;
      font-size: 12px;
      line-height: 16px;
      color: #464646;
    }

  }

  .dropdown-icon {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate3d(-10px, -50%, 0);
  }

  .Dropdown__List {
    padding: 0;
    min-height: 40px;
    max-height: 200px;
    overflow-y: auto;
  }
  .MuiListItemButton-root {
    height: 40px;
    border-bottom: 1px solid #EFEFEF;

    &.active {
      background-color: #EBF5FF;
    }
  }

  .List__Item {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-grow: 1;
    font-size: 14px;
    line-height: 16px;
    color: #464646;
  }

  .Item__Name, .Item__Amount {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
  }

  .Item__Amount {
    justify-content: flex-end;
    color: #9FA3A6;
  }

  #merchant-select-tooltip {
    position: absolute;
    top: 0;
    left: 100%;
    z-index: 33;
    transform: translate3d(4px, 10px, 0);
  }


}