#GasStation {
  width: 512px;
  height: 384px;

  .Container__Header {
    padding: 40px 0 0 0 !important;
  }

  .input-group {
    width: 100%;
    height: auto;
    position: relative;
    margin-bottom: 16px;
  }

  #currency {
    -webkit-box-shadow: none;
    background: rgba(245, 247, 251, 0.8);
  }

  #privateKey {
    padding: 12px 42px 12px 12px;
  }

  .currency-value {
    padding: 0 10px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    background: transparent;
    border-radius: 6px;

    &:hover {
      cursor: pointer;
    }

    svg {
      width: 16px;
      height: 16px;
    }

    .currency-symbol {
      display: inline-block;
      padding: 0 16px 0 8px;
      font: 400 14px/16px 'Roboto', sans-serif;
      color: #464646;
    }

  }

  .currency-dropdown {
    position: absolute;
    width: 100%;
    height: auto;
    min-height: 46px;
    top: 0;
    left: 0;
    z-index: 30;
    background: #FFFFFF;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }

  .dropdown-icon {
    width: 16px;
    height: 16px;
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translate3d(-30px, -8px, 0);
  }

  .currency-list {
    margin: 0;
    padding: 4px 0;
    list-style-type: none;
  }

  .currency-item {
    padding: 4px 8px;
    width: 100%;
    height: 36px;
    display: flex;
    align-items: center;
    background-color: #fff;
    transition: background-color 0.5s linear;

    &:hover {
      background-color: rgba(245, 247, 251, 0.8);
      cursor: pointer;
    }
  }

  .item-icon {
    display: flex;
    align-items: center;
    height: 100%;
    margin-right: 4px;

    svg {
      width: 16px;
      height: 16px;
    }
  }

  .item-symbol {
    display: flex;
    align-items: center;
    height: 100%;
    font: 400 14px/16px 'Roboto', sans-serif;
    color: #464646;
  }

  #gasStationForm {
    button.default:focus {
      opacity: 1;
    }
  }

}
