.LanguageSelector {
    position: relative;

    &__label {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        font-size: 14px;
        font-weight: 700;
        color: #9FA3A6;

        &:hover {
            cursor: pointer;
        }

        .arrow {
            transform: rotate(0deg);
            transition: transform 300ms linear;
        }

        .arrow.reversed {
            transform: rotate(-180deg);
        }
    }

    &__body {
        position: absolute;
        top: 38px;
        left: 50%;
        transform: translateX(-50%);
        background: #FFFFFF;
        border: 1px solid #FFFFFF;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
        border-radius: 3px;
        width: 89px;
        animation: fade 300ms linear;

        .locale-item {
            height: 40px;
            padding: 0 12px;
            display: flex;
            align-items: center;
            font-size: 12px;
            font-weight: 400;
            color: #464646;
            background: #FFFFFF;
            transition: background 300ms linear;
            user-select: none;

            &:hover {
                cursor: pointer;
                background: rgba(4, 85, 163, 0.04);
            }
        }

        .divider {
            width: 100%;
            height: 1px;
            background: #EFEFEF;
        }
    }
}

@keyframes fade {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}