.AddressesSelect {
  margin-bottom: 32px;
  padding: 0 12px;
  position: relative;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  background: #F5F7FB;
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  border-radius: 4px;

  &:hover {
    cursor: pointer;
  }

  .click-zone {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: transparent;

    &:hover {
      cursor: pointer;
    }
  }

  .AddressesSelect__Title {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 4px;
    transform: translate3d(0, -9px, 0);
    background: linear-gradient(transparent 50%, #F5F7FB 50%);
    color: rgba(156, 163, 166, 0.6);
    font-size: 12px;
    line-height: 16px;
    transition: all 0.3s ease-in-out;
  }

  .AddressesSelect__Placeholder {
    width: 100%;
    height: auto;
  }

  .Placeholder {
    width: calc(100% - 18px);
    height: auto;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    line-height: 16px;
    color: #464646;

    .value {
      font-weight: 600;
    }
  }

  .AddressesSelect__Dropdown {
    position: absolute;
    width: 100%;
    height: auto;
    top: 0;
    left: 0;
    z-index: 30;
    background: #FFFFFF;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    .Dropdown__Search {
      padding: 16px 14px;
      width: 100%;
      height: 88px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #EFEFEF;
    }

    .Search__Title {
      margin-bottom: 8px;
      font-size: 12px;
      line-height: 16px;
      color: #464646;
    }

  }

  .dropdown-icon {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate3d(-10px, -50%, 0);
  }

  .Dropdown__List {
    overflow-y: auto;
    padding: 0;
    max-height: 240px;
    min-height: 120px;
  }

  .MuiListItemButton-root {
    height: 40px;
    border-bottom: 1px solid #EFEFEF;

    &.active {
      background-color: #EBF5FF;
    }
  }

  .List__Item {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-grow: 1;
    font-size: 14px;
    line-height: 16px;
    color: #464646;
  }

  .Item__Checkbox, .Item__Amount, .Item__Risk {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
  }

  .Item__Checkbox {
    width: 36px;
  }

  .Item__Amount {
    width: 70%;
    color: #9FA3A6;
    align-items: flex-end;

    .value {
      padding-right: 4px;
    }

    .currency {
      font-size: 10px;
      line-height: 12px;
    }
  }

  .Item__Risk {
    position: relative;
    width: calc(30% - 24px);
    justify-content: flex-end;

    .percent {
      width: 30px;
      padding-left: 4px;
    }

    .icon-tooltip {
      position: relative;
      display: inline-block;
    }

    .icon-tooltip .icon-tooltip-text {
      visibility: hidden;
      width: 160px;
      height: 54px;
      background: #FFFFFF;
      border: 1px solid #EFEFEF;
      box-sizing: border-box;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      border-radius: 3px;
      text-align: center;
      padding: 5px 0;
      position: absolute;
      z-index: 1;
      top: 150%;
      left: 50%;
      margin-left: -150px;
      font-size: 12px;
      line-height: 14px;

      span {
        font-weight: 600;
        color: #0455A3;
      }
    }

    .icon-tooltip .icon-tooltip-text::after {
      content: "";
      position: absolute;
      bottom: 100%;
      left: calc(100% - 10px);
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: transparent transparent #EFEFEF transparent;
    }

    .icon-tooltip:hover .icon-tooltip-text {
      visibility: visible;
    }

    .check-icon {
      svg {
        width: 18px;
        height: 18px;

        path {
          fill: #0B8D57;
        }
      }
    }
  }

}