@import '../../assets/mixins/colors';

.profileModal {
  .profileModalTabs {
    display: flex;
    align-items: center;
    list-style-type: none;

    li {
      margin-right: 32px;
      color: #9FA3A6;
      font-size: 18px;
      font-weight: bold;
      line-height: 24px;
      transition: all 0.3s ease-in-out;
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }
      &:hover, &.active {
        color: #464646;
      }
    }
  }
}
