.icon {
    position: relative;

    &.disabled {
        opacity: 0.5;
    }

    .tooltip {
        position: absolute;
        display: none;
        top: 24px;
        padding: 8px;
        width: 140px;
        background: #FFFFFF;
        border: 1px solid #E6E6E6;
        box-shadow: 0 0 6px rgb(230, 230, 230);
        border-radius: 3px;
        color: #464646;
        font-size: 12px;
        font-style: normal;
        line-height: 14px;
        right: -10px;
        z-index: 1;

        &::before {
            content: "";
            position: absolute;
            top: -16px;
            right: 8px;
            display: block;
            background: transparent;
            border: 8px solid transparent;
            border-bottom: 8px solid #E6E6E6;
        }
    }

    &:hover .tooltip {
        display: block;
    }
}