@import '../../../assets/mixins/colors';

.mainTabMain {
  display: flex;
  flex-direction: column;
  width: 100%;

  .mainTabMainConnection {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;

    .mainTabMainConnectionItem {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-right: 16px;
      width: 15%;

      &:nth-child(2), &:nth-child(3) {
        .mainTabMainConnectionItemCountry {
          background: #0455A3;
          color: #FFFFFF;
        }
        .mainTabMainConnectionItemIcon {
          background: #0455A3;
        }
        .mainTabMainConnectionItemIP {
          background: #0455A3;
          color: #FFFFFF;
        }
      }
      .mainTabMainConnectionItemCountry {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: baseline;
        padding-top: 8px;
        width: 100%;
        height: 52px;
        background: #FFFFFF;
        border-radius: 8px;
        color: #464646;
        font-size: 14px;
        font-weight: 700;

        &:after {
          content: "";
          position: absolute;
          top: calc(100% + 10px);
          left: 50%;
          display: block;
          height: 2px;
          width: 100%;
          background: #18AA6E;
          z-index: 1;
        }

        .fi {
          font-size: 16px;
        }
      }
      .mainTabMainConnectionItemIcon {
        margin-top: -24px;
        width: 68px;
        height: 68px;
        background: #FFFFFF;
        border: 4px solid #F5F7FB;
        border-radius: 60px;
        z-index: 1;

        .mainTabMainConnectionItemIconContainer {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          border: 2px solid #18AA6E;
          border-radius: 100%;
          box-shadow: 0 0 16px rgba(0, 0, 0, 0.25);
        }
      }
      .mainTabMainConnectionItemIP {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        margin-top: -24px;
        padding-bottom: 8px;
        width: 100%;
        height: 52px;
        background: #FFFFFF;
        border-radius: 8px;
        color: #464646;
        font-size: 14px;
        line-height: 16px;
      }
    }
    .mainTabMainConnectionNodes {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 55%;

      .mainTabMainConnectionNode {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 8px;
        padding: 10px 0;
        width: 25%;
        background: #FFFFFF;
        border-radius: 8px;

        &:last-child {
          margin-right: 0;
        }
        p {
          margin-bottom: 8px;
          color: #464646;
          font-size: 10px;
          font-weight: 700;
          line-height: 12px;
        }
        span {
          color: #464646;
          font-size: 10px;
          line-height: 12px;
        }
      }
      .mainTabMainConnectionNodesTop {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;

        &:before {
          content: "";
          position: absolute;
          top: calc(100% + 10px);
          left: 0;
          display: block;
          height: 2px;
          width: 50%;
          background: #18AA6E;
          z-index: 1;
        }
      }
      .mainTabMainConnectionNodesIcon {
        margin-top: -24px;
        width: 68px;
        height: 68px;
        background: #FFFFFF;
        border: 4px solid #F5F7FB;
        border-radius: 60px;
        z-index: 1;

        .mainTabMainConnectionNodesIconContainer {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          border: 2px solid #18AA6E;
          border-radius: 100%;
          box-shadow: 0 0 16px rgba(0, 0, 0, 0.25);
        }
      }
      .mainTabMainConnectionNodesBottom {
        display: flex;
        align-items: center;
        margin-top: -24px;
        width: 100%;
      }
    }

    .fi {
      margin-right: 4px;
    }
  }
  .mainTabMainStat {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;

    .mainTabMainStatItem {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-right: 16px;
      padding: 16px;
      width: 100%;
      background: #FFFFFF;
      border-radius: 8px;

      &:last-child {
        margin-right: 0;
      }
      p {
        margin-bottom: 8px;
        color: #0455A3;
        font-size: 24px;
        font-weight: 900;
        line-height: 28px;
      }
      span {
        color: #464646;
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
  .mainTabMainRisks {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;

    h4 {
      margin-bottom: 8px;
      color: #464646;
      font-size: 14px;
      font-weight: 700;
      line-height: 16px;
    }
    .mainTabMainRisksContainer {
      display: flex;
      align-items: center;

      .mainTabMainRisksItem {
        display: flex;
        flex-direction: column;
        margin-right: 16px;
        padding: 8px;
        width: 100%;
        background: #FFFFFF;
        border-radius: 8px;
        transition: all 0.3s ease-in-out;
        cursor: pointer;

        &:last-child {
          margin-right: 0;
        }
        &:hover {
          box-shadow: 0 0 8px rgba(0, 0, 0, 0.24);
        }
        .mainTabMainRisksItemHeader {
          display: flex;
          margin-bottom: 16px;

          .icon {
            margin-right: 4px;

            &.gray svg path {
              fill: #DADADA;
            }
            &.green svg path {
              fill: #18AA6E;
            }
            &.yellow svg path {
              fill: #EACF45;
            }
            &.red svg path {
              fill: #FF5959;
            }
          }
          .mainTabMainRisksItemHeaderContainer {
            display: flex;
            flex-direction: column;

            .tooltip {
              margin-bottom: 4px;

              .icon {
                margin-top: -2px;
              }
              span {
                color: #464646;
                font-size: 14px;
                font-weight: 700;
                line-height: 16px;
              }
            }
            p {
              color: #9FA3A6;
              font-size: 10px;
              line-height: 12px;
            }
          }
        }
        .mainTabMainRisksItemRow {
          display: flex;
          margin-bottom: 16px;

          .mainTabMainRisksItemCol {
            margin-bottom: 0;
            width: 50%;
          }
        }
        .mainTabMainRisksItemCol {
          display: flex;
          flex-direction: column;
          margin-bottom: 16px;

          &:last-child {
            margin-bottom: 0;
          }
          p {
            margin-bottom: 4px;
            color: #0455A3;
            font-size: 18px;
            font-weight: 700;
            line-height: 21px;
          }
          span {
            color: #9FA3A6;
            font-size: 10px;
            line-height: 12px;
          }
        }
      }
    }
  }
  .mainTabMainLogs {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;

    h4 {
      margin-bottom: 8px;
      color: #464646;
      font-size: 14px;
      font-weight: 700;
      line-height: 16px;
    }
    .mainTabMainLogsContainer {
      padding: 8px 0;
      background: #FFFFFF;
      border-radius: 8px;

      &__table-wrapper {
        height: 174px;
        overflow: auto;
        padding: 0 8px;

        table {
          border-spacing: 0 4px;

          thead {
            position: sticky;
            top: 0;

            th {
              font-size: 14px;
              line-height: 16px;
              padding-bottom: 4px;
              padding-left: 8px;
              color: $darkGray;
              background: white;
              font-weight: normal;
              text-align: start;
            }

            .date-column {
              width: 160px;
            }

            .event-column {
              width: 345px;
            }
          }

          tbody {
            tr {
              background: $lightGray;

              td {
                font-size: 14px;
                line-height: 16px;
                padding: 8px 0 8px 8px;
                color: $darkGray;
                border: 0px solid #fff;
                border-style: none;
                vertical-align: top;

                &:first-child {
                  border-top-left-radius: 8px;
                  border-bottom-left-radius: 8px;
                }

                &:last-child {
                  border-top-right-radius: 8px;
                  border-bottom-right-radius: 8px;
                  padding-right: 8px;
                  word-break: break-word;
                }
              }
            }
          }
        }
      }
    }
  }
  .mainTabMainTransactions {
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;

    h4 {
      margin-bottom: 8px;
      color: #464646;
      font-size: 14px;
      font-weight: 700;
      line-height: 16px;
    }
    .mainTabMainTransactionsContainer {
      padding: 8px;
      background: #FFFFFF;
      border-radius: 8px;

      ul {
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        padding: 0 8px;
        width: 100%;
        list-style-type: none;

        li {
          margin: 0;
          width: 25%;
          color: #9FA3A6;
          font-size: 14px;
          font-weight: 400;
          line-height: 16px;

          &:first-child {
            width: 50%;
          }
        }
      }
      .mainTabMainTransactionsBody {
        display: flex;
        flex-direction: column;

        .mainTabMainTransactionsBodyItem {
          display: flex;
          align-items: center;
          margin-bottom: 4px;
          padding: 16px 8px;
          background: #F5F7FB;
          border-radius: 8px;

          p {
            width: 25%;
            color: #9FA3A6;
            font-size: 14px;
            line-height: 16px;

            &:first-child {
              width: 50%;
            }
          }
        }
      }
    }
  }
}
