@import '../../assets/mixins/colors';

.notificationsModal {
  .notificationsModalTop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    width: 100%;

    h5 {
      color: $black;
      font-size: 14px;
      font-weight: bold;
      line-height: 16px;
    }
    i {
      cursor: pointer;
    }
  }
  .notificationsModalNotification {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
    padding: 8px;
    width: 100%;
    background: $lightGray;
    border-radius: 4px;

    &:last-child {
      margin-bottom: 0;
    }
    .notificationsModalNotificationLeft {
      display: flex;
      flex-direction: column;

      label {
        margin-bottom: 4px;
        font-size: 14px;
        line-height: 16px;

        &.blue {
          color: $blue;
        }
        &.orange {
          color: $orange;
        }
      }
      p {
        color: $black;
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;
      }
      span {
        margin-top: 4px;
        color: $blue;
        font-size: 14px;
        line-height: 16px;

        &.blue {
          color: $blue;
        }
        &.red {
          color: $red;
        }
      }
    }
    .notificationsModalNotificationRight {
      display: flex;
      flex-direction: column;

      span {
        color: $darkGray;
        font-size: 10px;
        line-height: 12px;
        text-align: right;

        &:first-child {
          margin-bottom: 10px;
        }
      }
      p {
        margin-top: 10px;
        color: $black;
        font-size: 10px;
        line-height: 12px;
        text-align: right;
      }
    }
  }
}
