@import '../../assets/mixins/colors';

.calcModal {
  position: absolute;
  top: 52px;
  right: 0;
  display: flex;
  flex-direction: column;
  padding: 8px;
  width: 248px;
  background: #FFFFFF;
  border-radius: 4px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.24);
  z-index: 10;
  cursor: auto;

  h4 {
    margin-bottom: 8px;
    padding-bottom: 8px;
    color: #464646;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    border-bottom: 1px solid #E6E6E6;
  }
  .calcModalConvert {
    display: flex;
    flex-direction: column;

    .calcModalConvertItem {
      display: flex;
      align-items: center;

      &:first-child {
        margin-bottom: 4px;
      }
      &:last-child {
        margin-top: 4px;
      }
      .select {
        margin-right: 4px;
        width: 98px;
        flex-shrink: 0;

        span {
          padding: 0;
          height: 16px;
          background: transparent;
          border: none;
          font-weight: 700;
        }
        i {
          top: 0;
          right: 0;

          svg path {
            fill: #9FA3A6;
          }
        }
        ul {
          top: 18px;
          width: auto;

          li {
            white-space: nowrap;
          }
        }
      }
      .input input {
        padding: 4px;
        height: 24px;
        border: none;
        border-radius: 4px;
        background: #F5F7FB;
        -webkit-box-shadow: inset 0 0 0 50px #F5F7FB;
        text-align: right;
      }
    }
    i {
      cursor: pointer;
    }
  }
  .calcModalCourses {
    display: flex;
    flex-direction: column;
    margin-top: 16px;
    padding-top: 16px;
    border-top: 1px solid #E6E6E6;

    .calcModalCoursesCourse {
      display: flex;
      flex-direction: column;
      margin-bottom: 8px;
      padding-bottom: 8px;
      border-bottom: 1px dashed #EFEFEF;

      &:last-child {
        margin: 0;
        padding: 0;
        border: none;
      }
      p {
        display: flex;
        margin-bottom: 4px;
        color: #464646;
        font-size: 12px;
        font-weight: 700;
        line-height: 14px;

        span {
          margin-left: 4px;
          color: #9FA3A6;
          font-weight: 400;
        }
      }
      div {
        display: flex;
        justify-content: space-between;

        b {
          color: #464646;
          font-size: 14px;
          font-weight: 700;
          line-height: 16px;
        }
        p {
          margin: 0;
          font-size: 14px;
          line-height: 16px;
          font-weight: 400;

          &.calcModalCoursesCourseUp {
            svg {
              transform: rotate(90deg);

              path {
                fill: #0B8D57;
              }
            }
            span {
              color: #0B8D57;
            }
          }
          &.calcModalCoursesCourseDown {
            svg {
              transform: rotate(-90deg);

              path {
                fill: #FF6161;
              }
            }
            span {
              color: #FF6161;
            }
          }
          span {
            margin-left: 4px;
          }
        }
      }
    }
  }
}
