.progress {
  position: relative;
  width: 100%;
  height: 40px;
  background: #FFFFFF;
  border: 1px solid #E6E6E6;
  border-radius: 4px;

  @keyframes move {
    0% {
      background-position: 0 0;
    }
    100% {
      background-position: 20px 20px;
    }
  }
  .progressLine {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background: #0455A3;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-image: linear-gradient(-45deg, rgba(255, 255, 255, .2) 25%, transparent 25%, transparent 50%,
              rgba(255, 255, 255, .2) 50%, rgba(255, 255, 255, .2) 75%, transparent 75%, transparent);
      background-size: 20px 20px;
      animation: move 2s linear infinite;
      overflow: hidden;
      z-index: 1;
    }
  }
  span {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    color: #E6E6E6;
    font-size: 14px;
    font-weight: bold;
    line-height: 20px;
    text-align: center;
    z-index: 1;
  }
}