@import '../../assets/mixins/colors';

.tooltip {
  display: flex;
  align-items: center;

  span {
    margin-right: 4px;
    color: $black;
    font-size: 14px;
    line-height: 16px;
  }
  .tooltipIcon {
    position: relative;
    cursor: pointer;

    i:hover + .tooltipIconContainer {
      display: block;
    }
    .tooltipIconContainer {
      position: absolute;
      top: 24px;
      display: none;
      padding: 8px;
      width: 140px;
      background: $white;
      border: 1px solid $gray;
      box-shadow: $shadow2;
      border-radius: 3px;
      color: $black;
      font-size: 12px;
      line-height: 14px;
      white-space: pre-line;
      z-index: 1;

      &:hover {
        display: block;
      }
      &:before {
        content: "";
        position: absolute;
        top: -16px;
        display: block;
        background: transparent;
        border: 8px solid transparent;
        border-bottom: 8px solid $gray
      }
      &.left {
        right: -9px;

        &:before {
          right: 8px;
        }
      }
      &.right {
        left: -9px;

        &:before {
          left: 8px;
        }
      }
    }
  }
}
