@import '../../assets/mixins/colors';

.ErrorCatcher {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(40px);
  z-index: 3000;

  &__modal {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 508px;
    padding: 56px;
    background: #FFFFFF;
    border-radius: 8px;

    .error-animation {
      position: relative;

      .background {
        width: 120px!important;
        height: 120px!important;
        position: static;
        z-index: auto;
      }

      &__cross {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__header {
    color: $black;
    margin: 8px 0 16px;
    font-size: 26px;
    line-height: 32px;
    font-weight: 700;
  }

  &__text {
    color: $darkGray;
    font-size: 18px;
    line-height: 24px;
  }

  &__button {
    width: fit-content;
    margin-top: 32px;
    padding: 0 28px;
  }
}