@import '../../../../assets/mixins/colors';

.tableControls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  padding: 0 16px;

  .tableControlsFilters {
    display: flex;
    align-items: center;

    .tableControlsFiltersSearch {
      position: relative;
      margin-right: 16px;

      i {
        position: absolute;
        top: 4px;
        left: 4px;
      }
      .tableControlsFiltersSearchClear {
        top: 8px;
        left: auto;
        right: 8px;
        cursor: pointer;
      }
      input {
        padding: 7px 31px 7px 35px;
        background: #FFFFFF;
        border: 1px solid $gray;
        border-radius: 4px;
        color: $black;
        font-size: 14px;
        line-height: 16px;
        outline: none;
        -webkit-box-shadow: inset 0 0 0 50px $white;
        -webkit-text-fill-color: $black;
        transition: all 0.3s ease-in-out;

        &::placeholder {
          color: $darkGray;
          -webkit-text-fill-color: $darkGray;
          opacity: 1;
        }
        &:-ms-input-placeholder {
          color: $darkGray;
          -webkit-text-fill-color: $darkGray;
        }
        &::-ms-input-placeholder {
          color: $darkGray;
          -webkit-text-fill-color: $darkGray;
        }
        &:focus {
          border: 1px solid $blue;
        }
        &:disabled {
          background: $lightGray;
          -webkit-box-shadow: inset 0 0 0 50px $lightGray;
        }
      }
      input:not(:placeholder-shown) + label {
        opacity: 1;
      }
      input:focus + label {
        color: $blue;
        opacity: 1;
      }
    }
    .tableControlsFiltersDate {
      display: flex;
      align-items: center;
      margin-right: 32px;

      .icon {
        margin-right: 9px;
        cursor: pointer;
      }

      &__dates {
        font-size: 12px;
        line-height: 14px;
        color: $darkGray;
      }

      &__from {
        margin-bottom: 4px;
        cursor: pointer;
      }

      &__to {
        cursor: pointer;
      }

      &__date {
        width: 65px;
        font-size: 12px;
        line-height: 14px;
        color: $black;
        cursor: pointer;
        border: none;
        outline: none;
        caret-color: transparent;
      }
    }
  }
  .tableControlsPagination {
    display: flex;
    align-items: center;

    .tableControlsPaginationCount {
      display: flex;
      align-items: center;
      margin-right: 32px;

      label {
        margin-right: 8px;
        color: #9FA3A6;
        font-size: 14px;
        line-height: 24px;
      }
      .select {
        width: 32px;

        span {
          padding: 0;
          width: 16px;
          height: 16px;
          background: none;
          border: none;
          color: #9FA3A6;
          font-weight: 700;
        }
        i {
          top: 0;
          right: 0;
        }
        ul {
          top: 24px;
          width: 40px;
        }
      }
    }
    .tableControlsPaginationTotal {
      margin-right: 16px;
      color: #9FA3A6;
      font-size: 14px;
      line-height: 24px;
    }
    .tableControlsPaginationArrows {
      display: flex;
      align-items: center;

      i:last-child {
        transform: rotate(180deg);
      }
      i {
        cursor: pointer;

        &:hover svg path {
          fill: $blue;
        }
        &.disabled svg path {
          fill: #9FA3A6;
          fill-opacity: 0.4;
        }
        svg path {
          transition: all 0.3s ease-in-out;
        }
      }
    }
    .tableControlsPaginationSettings {
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      &:hover {
        transform: rotate(180deg);
      }
      svg path {
        fill: #9FA3A6;
      }
    }
  }
}