.TransactionCommission {
  margin-bottom: 16px;

  .TransactionCommission__Title {
    margin-bottom: 16px;
    width: 100%;
    height: auto;
    display: flex;

    .MuiTypography-h5 {
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
      color: #464646;
    }

    #transaction-commission-tooltip {
      .tooltipIconContainer {
        width: 180px;
      }
    }
  }

  .TransactionCommission__Subtitle {
    font-size: 14px;
    line-height: 16px;
    color: #FF6161;
  }

  .TransactionCommission__Tabs {
    padding: 4px;
    margin-bottom: 16px;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    background: #FFFFFF;
    border: 1px solid #9FA3A6;
    box-sizing: border-box;
    border-radius: 4px;

    .commission-tab {
      font-size: 14px;
      font-weight: 400;
      line-height: 16px;
      background-color: #FFFFFF;
      color: #9FA3A6;

      &.active {
        background: #0455A3;
        color: #FFFFFF;
      }
    }
  }

  .TransactionCommission__Input {
    .form-field {
      margin-bottom: 8px;
      position: relative;
      width: 100%;
      height: auto;

      #commission-input {
        padding-right: 30%;
      }

      .input-rate {
        padding: 0 12px;
        width: auto;
        height: 40px;
        display: flex;
        position: absolute;
        align-items: center;
        top: 0;
        right: 0;
        font-size: 14px;
        line-height: 16px;
        color: #9FA3A6;
      }

    }

    .input-label {
      text-align: center;
      font-size: 12px;
      line-height: 14px;
      color: #9FA3A6;
    }
  }
}
