@import '../../../../assets/mixins/colors';

.BalanceSheetTab {

  .control-block {
    display: flex;
    margin-bottom: 8px;
    padding: 0 16px;
    background: #ffffff;

    &__merchant-select,
    &__currency-select {
      width: 216px;
      padding: 8px 16px 8px 0;

      .control-block__select-label {
        font-size: 14px;
        line-height: 16px;
        color: $darkGray;
      }

      .select {
        margin-top: 4px;

        span {
          height: 32px;
          padding: 8px 12px;
        }

        > .icon {
          top: 8px
        }

        ul {
          top: 32px;
        }
      }
    }

    &__dates-select {
      padding: 8px 16px;
      border-left: 1px #efefef solid;

      .tableControlsFiltersDate {
        display: flex;

        > .icon {
          align-self: flex-end;
        }
  
        &__dates {
          display: flex;
        }
  
        &__from,
        &__to {
          display: flex;
          flex-direction: column;
          font-size: 14px;
          line-height: 16px;
          color: $darkGray;
          margin-left: 8px;
        }
  
        &__date {
          margin-top: 4px;
          border: 1px $gray solid;
          border-radius: 4px;
          background: $lightGray;
          padding: 7px 11px;
          color: $black;
          outline: none;
          font-size: 14px;
          line-height: 16px;
        }
      }
    }
  }

  .currency-column {
    display: flex;
    align-items: center;

    > .icon {
      margin-right: 8px;
    }

    .currency-postfix {
      color: $darkGray;
    }
  }
}