.tableSettingsModal .modalContainer {
  width: 100%;
  max-width: 612px;

  .modalContainerBody {

    .tableSettingsModalHiddenCripto {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 4px 0;
      border-top: 1px solid #F5F7FB;
      border-bottom: 1px solid #F5F7FB;
      margin-bottom: 12px;
    }

    .tableSettingsModalHeader {
      display: flex;
      align-items: center;
      padding: 16px 8px;
      width: 100%;
      list-style-type: none;
      border-bottom: 1px solid #F5F7FB;

      li {
        color: #9FA3A6;
        font-size: 12px;
        line-height: 16px;

        &:nth-child(2), &:nth-child(3) {
          text-align: center;
        }
      }
    }
    .tableSettingsModalBody {
      display: flex;
      flex-direction: column;
      margin-bottom: 32px;
      width: 100%;
      max-height: 50vh;
      overflow: auto;

      .tableSettingsModalBodyItem {
        display: flex;
        align-items: center;
        padding: 16px 0;
        border-bottom: 1px solid #F5F7FB;

        .tableSettingsModalBodyItemName {
          display: flex;
          align-items: center;

          i {
            margin-right: 8px;
            min-width: 16px;
          }
          span {
            color: #464646;
            font-size: 14px;
            line-height: 16px;
          }
        }
        .tableSettingsModalBodyItemCell {
          display: flex;
          justify-content: center;
        }
        .tableSettingsModalBodyItemRadio {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 16px;
          height: 16px;
          background: rgba(4, 85, 163, 0.1);
          border: 1px solid rgba(4, 85, 163, 0.6);
          border-radius: 16px;
          cursor: pointer;

          div {
            width: 10px;
            height: 10px;
            background: #0455A3;
            border-radius: 10px;
          }
        }
      }
    }
    button {
      width: 100%;
      max-width: 336px;
    }
  }
}