.CurrencySelect {
  margin-bottom: 13px;
  position: relative;
  width: 100%;
  height: 40px;
  display: flex;

  .Body {
    width: 100%;
    height: auto;
    display: flex;
    flex-grow: 1;

    &__Currency, &__Amount {
      width: 100%;
      height: auto;
      display: flex;
    }

    &__Currency {
      flex: 115px 0;
      flex-shrink: 0;
      height: 32px;
      align-items: center;

      &:hover {
        cursor: pointer;
      }
    }

    .Currency__Icon {
      margin-right: 6px;
      svg {
        width: 24px;
        height: 24px;
      }
    }

    .Currency__Name {
      position: relative;

      .MuiTypography-h4 {
        color: #464646;
        font-size: 16px;
      }

      .icon {
        position: absolute;
        width: 20px !important;
        height: 20px !important;
        top: 50%;
        right: 0;
        transform: translate(30px, -50%);
      }
    }

    &__Amount {
      flex-direction: column;
      align-items: flex-end;
    }

    .Amount__Value {
      .MuiTypography-body2 {
        padding-right: 4px;
        display: inline-block;
        color: #464646;
      }

      .MuiTypography-caption {
        font-size: 10px;
      }
    }

    .MuiTypography-caption {
      color: #9FA3A6;
    }
  }

  .Dropdown {
    position: absolute;
    width: 100%;
    height: auto;
    top: 0;
    left: 0;
    z-index: 30;
    background: #FFFFFF;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    .Dropdown__List {
      padding: 0;
      min-height: 40px;
      max-height: 120px;
      overflow-y: auto;
    }

    .Dropdown__Search {
      padding: 16px 14px;
      width: 100%;
      height: 88px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #EFEFEF;
    }

    .Search__Title {
      margin-bottom: 8px;
      font-size: 12px;
      line-height: 16px;
      color: #464646;
    }
  }

  .MuiListItemButton-root {
    height: 40px;
    border-bottom: 1px solid #EFEFEF;

    &.active {
      background-color: #EBF5FF;
    }
  }

  .List__Item {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-grow: 1;

    .Currency__Name {
      .MuiTypography-h4 {
        font-size: 14px;
        line-height: 16px;
      }
    }

    .Currency__Icon {
      svg {
        margin-right: 6px;
        width: 18px;
        height: 18px;
      }
    }
  }

  .Item__Currency, .Item__Value {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    .CurrencyIcon {
      margin-right: 6px;
      width: 16px;
      height: 16px;
    }
  }

  .Item__Value {
    justify-content: flex-end;
    align-items: flex-end;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;

    .Value__Number {
      padding-right: 6px;
    }

    .Value__Currency {
      font-size: 10px;
      line-height: 14px;
    }
  }
}
