@import '../../assets/mixins/colors';

.merchants {
  height: 100%;
  display: flex;
  flex-direction: column;

  .merchantsModal {
    .modalContainerBody {
      form {
        width: 100%;

        .input {
          margin-bottom: 16px;
        }
        .merchantsModalIsReusable {
          margin-bottom: 16px;
          display: inline-block;

          label  span {
            margin-left: 4px;
            margin-right: 5px;
            opacity: 1;
          }

          .tooltip {
            transform: none;
          }
        }
      }
    }
  }
  .merchantsHead {
    &__header {
      align-self: flex-start;
      display: flex;
      align-items: center;
    }
    
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;

    h3 {
      color: $blue;
      font-size: 18px;
      font-weight: bold;
      line-height: 24px;
    }
    .merchantsHeadContainer {
      display: flex;
      align-items: center;

      .merchantsHeadContainerExport {
        margin-right: 16px;
        height: 40px;
        width: 120px;

        svg path {
          fill: $white;
          fill-opacity: 1;
        }
      }
      .merchantsHeadContainerAdd {
        height: 40px;
      }
      .merchantsHeadContainerReference {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        width: 40px;
        height: 40px;

        i {
          margin-right: 0;
        }
      }
    }
  }

  .table-wrapper {
    height: calc(100% - 48px);
  }
}
