@import '../../../../../../assets/mixins/colors.scss';

.ReceivingCurrencySelect {
  position: relative;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  padding: 0 12px;
  background: $lightGray;
  border: 1px solid $gray;
  box-sizing: border-box;
  border-radius: 4px;

  .click-zone {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: transparent;
    &:hover {
      cursor: pointer;
    }
  }

  .currency-name {
    margin-left: 8px;
  }

  .dropdown-icon {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate3d(-10px, -50%, 0);
  }

  .dropdown {
    position: absolute;
    width: 100%;
    height: auto;
    top: calc(100% + 1px);
    left: 0;
    z-index: 30;
    background: #FFFFFF;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    &__search {
      padding: 16px 14px;
      width: 100%;
      height: 88px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #EFEFEF;

      .search-title {
        margin-bottom: 8px;
        font-size: 12px;
        line-height: 16px;
        color: $black;
      }
    }

    &__list {
      padding: 0;
      max-height: 120px;
      overflow-y: auto;

      &::-webkit-scrollbar-thumb {
        background-color: $darkGray;
      }
    }

    &__item {
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      flex-grow: 1;
    }

    .item {
      &__currency {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
      }

      &__currency-name {
        margin-left: 4px;

        .MuiTypography-h4 {
          font-size: 14px;
          line-height: 16px;
        }
      }

      &__value {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        align-items: flex-end;
        font-size: 14px;
        line-height: 16px;
        color: $darkGray;
      }

      &__value-number {
        padding-right: 6px;
      }

      &__value-currency {
        font-size: 10px;
        line-height: 14px;
      }
    }

    .MuiListItemButton-root {
      height: 40px;
      border-bottom: 1px solid #EFEFEF;

      &.active {
        background-color: #EBF5FF;
      }
    }
  }
}