#RootModal {
  #Backup {
    width: 688px;
    height: 644px;

    .Container__Header {
      margin: 0;
    }

    .Container__Body {
      padding: 20px 72px 0 72px;
    }

    .Backup {
      .SecurityPassword__Title,
      .Account__Title {
        margin-bottom: 24px;

        h3 {
          font: 400 18px/24px 'Roboto';
          text-align: center;
          color: #9FA3A6;
        }
      }

      .SecurityPassword__Subtitle,
      .Account__Subtitle {
        margin-bottom: 16px;

        h4 {
          font: 700 14px/16px 'Roboto';
          color: #464646;
        }
      }

      .Body {
        margin-bottom: 24px;
        padding: 0 80px 0 80px;
      }

      #security-password {
        padding-right: 52px;
        color: #464646;
      }

      .Form__Field {
        width: 100%;
        height: 100%;
        position: relative;
      }

      .Form__Copy {
        width: 24px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        position: absolute;
        top: 0;
        left: 100%;
        transform: translate3d(calc(-200% - 4px), 0, 0);

        &:hover {
          cursor: pointer;
        }
      }

      .Form__Show {
        width: 24px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        position: absolute;
        top: 0;
        left: 100%;
        transform: translate3d(calc(-100% - 4px), 0, 0);

        &:hover {
          cursor: pointer;
        }
      }

      .Form__Generate {
        width: 24px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        position: absolute;
        top: 0;
        left: 100%;
        transform: translate3d(4px, 0, 0);

        &:hover {
          cursor: pointer;
        }
      }

      .Form__HelpText {
        margin: 16px 0;
        font: 400 14px/16px 'Roboto';
        color: #9FA3A6;
        text-align: center;

        &.error {
          color: #FF5959;
        }
      }

      .Account__Button {
        width: 100%;
        margin-bottom: 24px;
        text-transform: initial;

        &.done {
          margin-bottom: 12px;
          padding-left: 0;
          justify-content: flex-start;
          border-color: transparent;
          color: #9FA3A6;
        }
      }

      .SignIn {
        width: 100%;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        font: 700 14/16px 'Roboto';
        color: #464646;
        border-radius: 4px;
        border: 1px solid #464646;
        text-decoration: none;

        &.disabled {
          opacity: 0.6;
          cursor: pointer;
        }
      }
    }

    legend {
      display: none;
    }

    .Mui-error {
      legend {
        display: block;
      }
    }
  }
}
