.fundsAutoWithdrawals {
  height: calc(100% - 48px);
  
  .tableWrapper {
    width: 100%;
    height: 100%;
  }

  .tableBodyItemCellSwitch {
    display: flex;
    align-items: center;

    &.tableBodyItemCellSwitchDisabled span {
      color: rgba(70, 70, 70, 0.5);
    }
    span {
      margin-left: 8px;
      color: #464646;
      font-size: 12px;
      line-height: 16px;
    }
  }
}