.withdrawModal {
  .modal .modalContainer {
    width: 100%;
    max-width: 512px;

    .modalContainerBody {
      padding: 0;

      .select, .input {
        margin-bottom: 16px;
      }
      .withdrawModalForm {
        padding: 8px 88px 32px 88px;
        width: 100%;
        overflow: auto;

        .withdrawModalCommission {
          width: 100%;

          p {
            margin-bottom: 16px;
            color: #464646;
            font-size: 14px;
            font-weight: bold;
            line-height: 16px;
            text-align: left;
          }
        }
        .withdrawModalRecommendedCommission {
          margin-bottom: 24px;
          width: 100%;

          .tooltip {
            margin-bottom: 16px;

            span {
              color: #464646;
              font-size: 14px;
              font-weight: bold;
              line-height: 16px;
              text-align: left;
            }
          }
          .withdrawModalRecommendedCommissionContainer {
            padding: 16px 8px;
            width: 100%;
            border: 1px dashed #E6E6E6;
            border-radius: 4px;

            .withdrawModalRecommendedCommissionContainerItem {
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-bottom: 8px;

              &:last-child {
                margin-bottom: 0;
              }
              span {
                color: #464646;
                font-size: 14px;
                line-height: 24px;
              }
            }
          }
        }
      }
      .withdrawModalAccept {
        padding: 8px 88px 32px 88px;
        width: 100%;
        overflow: auto;

        .withdrawModalInfoContainer {
          display: flex;
          align-items: center;

          .withdrawModalInfo {
            width: 50%;
          }
        }
        .withdrawModalInfo {
          display: flex;
          flex-direction: column;
          margin-bottom: 16px;
          padding-bottom: 16px;
          width: 100%;
          border-bottom: 1px solid #EFEFEF;

          &:nth-child(7) {
            padding-bottom: 16px;
            border-bottom: none;
          }
          label {
            margin-bottom: 8px;
            color: #9FA3A6;
            font-size: 14px;
            line-height: 16px;
          }
          span {
            color: #464646;
            font-size: 14px;
            line-height: 16px;
          }
          .withdrawModalInfoContainer {
            display: flex;
            flex-direction: column;
            max-height: 150px;
            overflow: auto;
          }
          .withdrawModalInfoAddress {
            margin-bottom: 8px;
            width: 100%;
            color: #0455A3;
            font-size: 14px;
            line-height: 16px;

            &:last-child {
              margin-bottom: 0;
            }
          }
        }
        .withdrawModalAcceptCheckbox {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: 16px;

          span {
            opacity: 1;
          }
        }
        .withdrawModalNotice {
          margin-bottom: 24px;
          color: #9FA3A6;
          font-size: 14px;
          line-height: 16px;
          text-align: center;
        }
      }
      .withdrawModalSuccess {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 88px 88px 88px;
        width: 100%;

        i {
          margin-bottom: 32px;

          svg path {
            fill: #00A86D;
          }
        }
        h2 {
          margin-bottom: 24px;
          color: #464646;
          font-size: 26px;
          font-weight: bold;
          line-height: 32px;
          text-align: center;
        }
        p {
          margin-bottom: 8px;
          color: #9FA3A6;
          font-size: 18px;
          line-height: 24px;
          text-align: center;
        }
        span {
          color: #464646;
          font-size: 14px;
          line-height: 16px;
          text-align: center;
          text-decoration: underline;
          word-break: break-all;
        }
      }
      .withdrawModalError {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 88px 88px 88px;
        width: 100%;

        i {
          margin-bottom: 32px;
        }
        h2 {
          margin-bottom: 24px;
          color: #464646;
          font-size: 26px;
          font-weight: bold;
          line-height: 32px;
          text-align: center;
        }
        p {
          color: #9FA3A6;
          font-size: 18px;
          line-height: 24px;
          text-align: center;
        }
      }
    }
  }
}