@import '../../assets/mixins/colors';

.popup {
  position: relative;
  width: max-content;

  .popupIcon {
    display: flex;
    align-items: center;
    cursor: pointer;

    &:hover {
      i {
        transform: rotate(180deg);
      }
      & + .popupWindow {
        display: block;
      }
    }
    span {
      color: #464646;
      font-size: 14px;
      line-height: 16px;
    }
    i {
      transform: rotate(0deg);
      transition: all 0.3s ease-in-out;
    }
  }
  .popupWindow {
    position: absolute;
    top: calc(100% + 8px);
    left: 50%;
    display: none;
    width: max-content;
    background: #FFFFFF;
    border: 1px solid #F5F7FB;
    border-radius: 3px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    transform: translateX(-50%);
    z-index: 1;

    &:hover {
      display: block;
    }

    &:before {
      content: "";
      position: absolute;
      top: -16px;
      left: calc(50% - 8px);
      display: block;
      background: transparent;
      border: 8px solid transparent;
      border-bottom: 8px solid #F5F7FB;
    }

    div {
      padding: 8px 16px;
      height: 100%;
      overflow: auto;
      max-height: 150px;

      p {
        padding: 8px 0;
        color: #0455A3;
        font-size: 12px;
        line-height: 16px;
        border-bottom: 1px solid #F5F7FB;

        &:last-child {
          border-bottom: none;
        }
      }
    }
  }
}
