@import '../../../../assets/mixins/colors';

.tableContent {
  display: flex;
  flex-direction: column;
  overflow: auto;
  height: 100%;
  position: relative;

  .tableCheckBox {
    margin: 2px 12px 2px 0;
  }

  .selecdedItemsActions {
    display: flex;
    flex-grow: 1;
    position: absolute;
    left: 48px;

    &__hiddeAddresses {
      margin-right: 16px;
      padding: 8px 16px;
      color: #0455A3;
      background: rgba(4, 85, 163, .1);
      border: 1px solid #0455A3;
      border-radius: 4px;
      cursor: pointer;
    }

    &__checkAdresses {
      color: $green;
      cursor: pointer;
    }

    &__sendToQuarantine {
      margin-left: 36px;
      color: $red;
      cursor: pointer;
    }
  }

  .tableHead {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: fit-content;
    min-width: 100%;
    padding: 16px;
    background: white;
    border-bottom: 1px solid $gray;
    z-index: 5;

    .block-select-all {
      position: relative;
      margin: 2px 12px 2px 0;

      .tableCheckBox {
        margin: 0;
      }

      #selectAll:hover ~ .tooltip-select-all {
        opacity: 1;
      }
    }

    .tooltip-select-all {
      position: absolute;
      opacity: 0;
      left: -9px;
      top: 26px;
      width: max-content;
      padding: 6px 12px;
      background-color: #fff;
      border: 1px solid #EFEFEF;
      border-radius: 3px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, .1);
      transition: all .3s;

      &:before {
        content: "";
        position: absolute;
        top: -17px;
        left: 8px;
        display: block;
        background: transparent;
        border: 8px solid transparent;
        border-bottom: 8px solid #E6E6E6;
      }
    }

    .tableHeadColumn {
      display: flex;
      align-items: center;
      margin-right: 32px;
      white-space: pre-line;

      &:last-child {
        margin-right: 0;
      }

      span {
        color: $darkGray;
        font-size: 14px;
        line-height: 16px;
        cursor: pointer;
      }

      .withoutCursor {
        cursor: auto;
      }

      .tableHeadColumnSort {
        margin-left: 8px;
        min-width: 10px;
        cursor: pointer;

        svg path {
          fill: $darkGray;
          transition: all 0.3s ease-in-out;
        }

        &.tableHeadColumnSortDesc svg path:last-child,
        &.tableHeadColumnSortAsc svg path:first-child {
          fill: $blue;
          stroke: $blue;
          stroke-width: 1;
        }
      }

      .tooltip {
        margin-left: 2px;
      }
    }
  }

  .tableBody {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-width: 100%;
    width: fit-content;

    .tableBodyEmpty {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 80px 0;

      i {
        margin-bottom: 36px;
      }
      p {
        color: rgba(159, 163, 166, 0.5);
        font-size: 24px;
        line-height: 24px;
        text-align: center;
      }
    }
    .tableBodyItemWrapper {
      padding: 0 16px;

      &-active {
        background: rgba(4, 85, 163, 0.05);

        .tableBodyItem {
          background: transparent;
        }
      }

      &.hidden {
        opacity: 0.5;
      }
    }
    .tableBodyItem {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 0;
      width: 100%;
      border-bottom: 1px solid $gray;
      background: rgba(255, 255, 255, 1);
      transition: background 300ms linear;

      .tableBodyItemCell {
        margin-right: 32px;
        color: $black;
        font-size: 14px;
        line-height: 16px;
        word-break: break-word;

        &:last-child {
          margin-right: 0;
        }
        .tableBodyItemCellLink {
          display: flex;
          align-items: center;
          color: #0455A3;
          text-decoration: none;
          cursor: pointer;

          .icon {
            margin-top: -2px;
            margin-right: 8px;
          }
        }
        .tableBodyItemCellButton {
          width: fit-content;
          height: auto;
          display: flex;
          align-items: center;
          color: $blue;
          text-decoration: none;
          font-weight: 400;
          background-color: #0455a31a;
          border: none;
          border-radius: 4px;
          padding: 4px 8px;
          cursor: pointer;

          .icon {
            margin-right: 8px;
          }

          i svg path {
            fill: #0455A3;
            stroke: none;
          }
        }
        .tableBodyItemCellBalance {
          display: flex;
          align-items: center;
          // cursor: pointer;

          i {
            margin-right: 8px;
            flex-shrink: 0;
          }
          p {
            display: flex;
            flex-direction: column;

            b {
              margin-bottom: 2px;
              color: #464646;
              font-size: 14px;
              font-weight: bold;
              line-height: 16px;
            }
            span {
              color: #9FA3A6;
              font-size: 12px;
              line-height: 14px;
            }
          }
        }
        .tableBodyItemCellAddress {
          display: flex;

          &__link {
            width: 200px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            margin-right: 7px;
            color: #0455A3;
            cursor: pointer;

            &:before, &:after {
              display: inline-block;
              max-width: 50%;
              overflow: hidden;
              white-space: pre;
              text-decoration: underline;
            }

            &:before {
              content: attr(data-content-start);
              text-overflow: ellipsis;
            }

            &:after {
              content: attr(data-content-end);
              text-overflow: clip;
              direction: rtl;
            }
          }

          .icon {
            cursor: pointer;
          }

          .block-notification {
            position: relative;
          }

          .copy-notice {
            position: absolute;
            opacity: 0;
            left: 50%;
            top: 26px;
            width: max-content;
            padding: 6px 12px;
            transform: translateX(-50%);
            background-color: #fff;
            border: 1px solid #EFEFEF;
            border-radius: 3px;
            box-shadow: 0 2px 8px rgba(0,0,0,.1);
            transition: all .3s;

            &:before {
              content: "";
              position: absolute;
              top: -16px;
              left: calc(50% - 8px);
              display: block;
              background: transparent;
              border: 8px solid transparent;
              border-bottom: 8px solid #E6E6E6;
            }

            &.visible{ 
              opacity: 1;
            }
          }
        }
        .tableBodyItemCellActions {
          display: flex;
          align-items: center;

          i, a {
            margin-right: 16px;
            cursor: pointer;

            &:last-child {
              margin-right: 0;
            }
          }
          .tableBodyItemCellActionsTrash svg path {
            fill: $red;
          }
        }

        .commentBox {
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          display: flex;
          
          span {
            overflow: hidden;
          }

          i {
            margin-left: 4px;
            flex-shrink: 0;
            cursor: pointer;
          }

          // &__start {
          //   max-width: 50%;
          //   overflow: hidden;
          // }
          // &__end {
          //   max-width: 50%;
          //   direction: rtl;
          //   overflow: hidden;
          //   white-space: nowrap;
          //   text-overflow: ellipsis;
          // }
        }

        $colors: (
          darkGray: $darkGray,
          orange: $orange,
          green: $green,
          red: $red,
        );

        .text-color {
          @each $name, $color in $colors {
            &-#{$name} {
              color: $color;
            }
          }
        }
        

        .flex-table-cell {
          display: flex;
        }
      }
    }
  }

  .tableFooter {
    position: sticky;
    left: 0;
    bottom: 0;
    background-color: #F2F6FA;
    display: flex;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    padding: 8px 16px;

    .item {
      display: flex;
      margin-left: 16px;

      i {
        margin-right: 8px;
      }

      span:last-child {
        margin-left: 4px;
      }
    }
  }
}