.TransactionSuccess {
  width: 100%;
  height: auto;

  .TransactionSuccess__Icon {
    margin-bottom: 6px;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .TransactionSuccess__Title {
    margin-bottom: 18px;
    font-weight: bold;
    font-size: 26px;
    line-height: 32px;
    color: #464646;
    text-align: center;
  }

  .TransactionSuccess__Label {
    margin-bottom: 6px;
    font-size: 18px;
    line-height: 24px;
    color: #9FA3A6;
    text-align: center;
  }

  .TransactionSuccess__Transaction {
    position: relative;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    .address {
      width: 90%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin-right: 6px;
      font-size: 14px;
      line-height: 16px;
      color: #0455A3;
    }

    .icon {
      transform: translate3d(0, 2px, 0);
      &:hover {
        cursor: pointer;
      }
    }
  }
}

#RootModal .RootModal__Container {
  &.withdrawal-success {
    height: 344px;

    .Container__Header {
      border: none !important;
    }

    .Container__Body {
      padding: 0 56px 0 56px !important;
    }
  }
}