.depositModal {
  .modal .modalContainer {
    width: 100%;
    max-width: 867px;

    .modalContainerBody {
      padding: 0 40px 56px 40px;

      .depositModalPrevious {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 16px;
        width: 100%;

        div {
          display: flex;
          flex-direction: column;
          padding: 16px;
          width: 100%;
          border: 1px dashed #E6E6E6;
          border-radius: 8px;

          &:first-child {
            margin-right: 8px;
          }
          &:last-child {
            margin-left: 8px;
          }
          span {
            color: #9FA3A6;
            font-size: 16px;
            line-height: 24px;
          }
          p {
            color: #464646;
            font-size: 16px;
            font-weight: bold;
            line-height: 24px;
          }
        }
      }
      .depositModalRecommended {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 16px;
        padding: 16px;
        width: 100%;
        border: 1px dashed #0455A3;
        border-radius: 8px;

        span {
          margin-right: 8px;
          color: #9FA3A6;
          font-size: 16px;
          line-height: 24px;
        }
        p {
          color: #464646;
          font-size: 16px;
          font-weight: bold;
          line-height: 24px;
        }
      }
      .depositModalAmount {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 16px;
        padding: 16px;
        width: 100%;
        border: 1px dashed #E6E6E6;
        border-radius: 8px;

        p {
          margin-bottom: 8px;
          color: #9FA3A6;
          font-size: 16px;
          line-height: 24px;
          text-align: center;
        }
        .input {
          width: 100%;
          max-width: 200px;

          input {
            text-align: center;
          }
        }
      }
      .depositModalAddresses {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 24px;
        width: 100%;
        border: 1px dashed #E6E6E6;
        border-radius: 8px;

        h4 {
          margin-bottom: 24px;
          color: #464646;
          font-size: 14px;
          font-weight: bold;
          line-height: 24px;
          text-align: center;
        }
        .depositModalAddressesTable {
          width: 100%;

          .depositModalAddressesTableHead {
            display: flex;
            align-items: center;
            padding: 16px 8px;
            border-bottom: 1px solid #EFEFEF;

            span {
              display: block;
              color: #9FA3A6;
              font-size: 14px;
              line-height: 16px;
            }
          }
          .depositModalAddressesTableBody {
            width: 100%;

            .depositModalAddressesTableBodyRow {
              display: flex;
              align-items: center;
              padding: 16px 8px;
              width: 100%;
              border-bottom: 1px solid #EFEFEF;

              span {
                color: #464646;
                font-size: 14px;
                font-weight: bold;
                line-height: 16px;
              }
              div {
                display: flex;
                align-items: center;

                p {
                  margin-right: 8px;
                  color: #0455A3;
                  font-size: 14px;
                  line-height: 16px;
                }
              }
            }
          }
        }
      }
    }
  }
}