@import '../../assets/mixins/colors';


.twoFaActivateModal {
  .modal .modalContainer {
    max-width: 512px;
    position: absolute;
    margin-right: -170%;
    transition: margin .49s;

    .modalContainerHeader {
      padding-bottom: 16px;
      
      .iconStep {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 32px;
        margin-top: 32px;

        hr {
          width: 72px;
          border: none;
          border-bottom: 2px solid #c4c4c4;
        }

        .svg-stepOne rect{
          fill-opacity: .1;
        }

        &.stepTwo, &.stepThree {
          .svg-stepOne {
            rect {
              fill-opacity: 1;
            }
            path {
              fill: $white;
            }
          }

          .svg-stepTwo {
            rect {
              fill-opacity: .1;
              fill: $blue;
            }
            path {
              fill: $blue;
            }
          }

          hr:nth-child(2) {
            border-color: $blue;
          }
        }

        &.stepThree {
          .svg-stepTwo {
            rect {
              fill-opacity: 1;
            }
            path {
              fill: $white;
            }
          }

          .svg-stepThree {
            rect {
              fill-opacity: .1;
              fill: $blue;
            }
            path {
              fill: $blue;
            }
          }

          hr:nth-child(4) {
            border-color: $blue;
          }
        }
      }
    }

    .modalContainerBody {
      .arrow-left {
        margin-right: 11px;
      }

      .arrow-right {
        margin-left: 11px;
      }

      button {
        font-weight: 700;

        &.light:hover svg path {
          fill: $white;
        }
      }

      .twoFaActivateModalStep0 {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        max-width: 476px;

        .twoFaActivateModalStep0Links {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 56px;

          img {
            height: 64px;
            cursor: pointer;
          }
        }
        button {
          width: 100%;
          max-width: 164px;
        }
      }

      .twoFaActivateModalStep1 {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        max-width: 552px;

        .twoFaActivateModalStep1Container {
          display: flex;
          margin-bottom: 40px;
          width: 100%;

          .twoFaActivateModalStep1ContainerContent {
            position: relative;
            width: 100%;
            
            input {
              padding: 12px 62px 12px 12px;
              background: $white;
              box-shadow: none;
            }

            .copyTwoFA {
              position: absolute;
              cursor: pointer;
              top: 50%;
              right: 37px;
              height: 16px;
              transform: translateY(-50%);
            }

            .openQR {
              position: absolute;
              cursor: pointer;
              top: 50%;
              right: 14px;
              height: 16px;
              transform: translateY(-50%);
            }

            .modal-qr {
              opacity: 0;
              z-index: -1;
              position: absolute;
              background: $white;
              right: 34px;
              top: 50%;
              transform: translateY(-50%);
              border-radius: 4px;
              box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
              transition: all .2s;
              // padding: 16px;
              height: 230px;
              width: 230px;

              &.active {
                opacity: 1;
                z-index: 101;
              }

              img {
                width: 100%;
                height: 100%;
              }
            }

            p {
              color: $black;
              font-size: 14px;
              line-height: 20px;
              text-align: center;
            }
          }
        }

        .twoFaActivateModalStep1Buttons {
          display: flex;
          align-items: center;
          width: 100%;

          button {
            width: 100%;

            &:first-child {
              margin-right: 4px;
            }
            &:last-child {
              margin-left: 4px;
            }
          }
        }
      }

      .twoFaActivateModalStep2 {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        max-width: 452px;

        .twoFaActivateModalStep2Error {
          // margin-top: -24px;
          margin-bottom: 28px;
          color: $red;
          font-size: 18px;
          line-height: 24px;
        }
        .twoFaActivateModalStep2Code {
          display: flex;
          align-items: center;
          margin-bottom: 32px;

          .input {
            margin-right: 24px;

            &:last-child {
              margin-right: 0;
            }
            input {
              width: 48px;
              min-height: 48px;
              text-align: center;
            }
          }
          &.error input {
            border-color: $red;
          }
        }
        button {
          width: 100%;
          max-width: 164px;
        }
      }
    }
  }

  .modal.active .modalContainer{
    margin-right: 0;
    margin-left: 0;
  }

  .modal.close .modalContainer{
    margin-left: -170%;
    margin-right: 0;
  }

  .modal.hidden .modalContainer{
    opacity: 0;
    margin-left: 0;
    margin-right: -170%;
    transition: none;
  }

  .modal.close-hidden .modalContainer{
    margin-left: -170%;
    margin-right: 0;
    opacity: 0;
    transition: none;
  }
}
