.ReceiverAddress {
  margin-bottom: 16px;
  position: relative;

  .check-icon {
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translate3d(6px, -50%, 0);
    
    &:hover {
      cursor: pointer;
    }

    svg {
      path {
        fill: #0B8D57;
      }
    }
    
  }

  .risk-status {
    position: absolute;
    top: 0;
    left: 100%;
    width: 32px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transform: translate3d(-125%, 0, 0);
  }

  #receiver-address-input {
    padding-right: 36px;
  }
}