#RootModal {
  #AutoWithdrawal {

    .Container__Body {
      overflow: auto;
      padding-top: 16px;
      padding-bottom: 56px;
    }

    .Container__Header {
      margin: 0;
      padding-bottom: 16px;
      border-bottom: 1px solid #EFEFEF;
    }

    .autoWithdrawModalAddresses {
      position: relative;
      margin-top: 16px;
      margin-bottom: 20px;

      p {
        font-size: 12px;
        font-weight: 400;
        color: #9Fa3A6;
      }

      .textarea-tooltip {
        position: absolute;
        left: 100%;
        top: 0;
        z-index: 33;
        transform: translate3d(4px, 10px, 0);
      }
    }

    .autoWithdrawModalActive {
      display: flex;
      align-items: center;
      margin-bottom: 24px;

      span {
        margin-left: 12px;
        font-size: 12px;
      }
    }

    .autoWithdrawal-send {
      margin-top: 32px;
    }
  }
}