.ConfirmationButtonsGroup {
  padding-bottom: 56px;
  width: 100%;
  height: auto;

  .field-group {
    margin-bottom: 12px;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 16px;
    color: #464646;
  }

  .ConfirmationButtonsGroup__Actions {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .MuiButton-root {
      width: calc(50% - 4px);
      text-transform: capitalize;
    }
  }

  .confirm-data-additional {
    margin-bottom: 18px;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;
    text-align: center;
  }

  .confirm-data-checkbox {
    display: flex;
    align-items: center;
    &:hover {
      cursor: pointer;
    }
  }
}