@import 'assets/mixins/responsive-init';
@import 'assets/mixins/responsive';
@import 'assets/mixins/colors';
@import 'assets/mixins/breakpoints';

@font-face {
  font-family: 'Roboto';
  font-weight: 100;
  src: local('Roboto'), url(assets/fonts/Roboto-Thin.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 300;
  src: local('Roboto'), url(assets/fonts/Roboto-Light.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  src: local('Roboto'), url(assets/fonts/Roboto-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 500;
  src: local('Roboto'), url(assets/fonts/Roboto-Medium.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  src: local('Roboto'), url(assets/fonts/Roboto-Bold.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-weight: 900;
  src: local('Roboto'), url(assets/fonts/Roboto-Black.ttf) format('truetype');
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  &::-webkit-scrollbar {
    width: size(6px);
    height: size(6px);
  }
  &::-webkit-scrollbar-track {
    background: $loginBackground;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $blue;
    border-radius: 6px;
  }
}
html, body {
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  font-size: size(16px);
  color: $black
}
.app {
  position: relative;
  min-height: 100vh;
}

// Disabled filters
.table .filter {
  display: none;
}

iframe {
  z-index: -1 !important;
}

.MuiButton-root {

  &.text-initial {
    text-transform: initial;
  }

  &.text-padding {
    padding-left: 12px;
    padding-right: 12px;
  }

  svg {
    margin-right: 8px;
  }

}

// Main Window Resize Options

body,
.app,
.menuContainer {
  min-width: 1366px;
  min-height: 768px;
  overflow: auto;
}



