@import '../../assets/mixins/colors';

.textarea {
  position: relative;
  width: 100%;

  label {
    position: absolute;
    top: -8px;
    left: 8px;
    padding: 0 4px;
    background: $white;
    color: $darkGray;
    font-size: 12px;
    line-height: 16px;
    transition: all 0.3s ease-in-out;
  }
  textarea {
    padding: 12px;
    width: 100%;
    height: 120px;
    background: $white;
    border: 1px solid $gray;
    border-radius: 4px;
    color: $black;
    font-size: 14px;
    line-height: 16px;
    outline: none;
    -webkit-box-shadow: inset 0 0 0 50px $white;
    -webkit-text-fill-color: $black;
    transition: all 0.3s ease-in-out;
    resize: none;

    &::placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
      opacity: 1;
    }
    &:-ms-input-placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
    }
    &::-ms-input-placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
    }
    &:focus {
      border: 1px solid $blue;
    }
    &:disabled {
      background: $lightGray;
      -webkit-box-shadow: inset 0 0 0 50px $lightGray;

      & + label {
        background: linear-gradient(transparent 50%, #F5F7FB 50%);
      }
    }
  }
  textarea:not(:placeholder-shown) + label {
    opacity: 1;
  }
  textarea:focus + label {
    color: $blue;
    opacity: 1;
  }
  &.textareaError {
    textarea {
      border: 1px solid $darkRed;
      color: $darkRed;
      -webkit-text-fill-color: $darkRed;
    }
    label {
      color: $darkRed !important;
    }
  }
}
