@import '../../../../../../assets/mixins/colors.scss';

.ReceivingAddressField {
  padding: 0 12px;
  position: relative;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  border: 1px solid $gray;
  box-sizing: border-box;
  border-radius: 4px;

  &__title {
    position: absolute;
    top: 0;
    left: 8px;
    padding: 0 4px;
    transform: translate3d(0, -9px, 0);
    background: linear-gradient(transparent 50%, $lightGray 50%);
    color: rgba(156, 163, 166, 0.6);
    font-size: 12px;
    line-height: 16px;
    transition: all 0.3s ease-in-out;
  }

  &__value {
    font-size: 14px;
    line-height: 16px;
    width: 296px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: $blue;
  }

  &__copy {
    cursor: pointer;
  }
}