$R2560 : 2560px;
$R1920 : 1920px;
$R1600 : 1600px;
$R1366 : 1366px;
$R1280 : 1280px;
$R1024 : 1024px;
$R768 : 768px;
$R576 : 576px;

@mixin BPR2560 {
  @media only screen and (min-width: $R2560) {
    @content;
  }
}

@mixin BPR1920 {
  @media only screen and (min-width: $R1600) and (max-width: ($R1920 - 0.02px)) {
    @content;
  }
}

@mixin BPR1600 {
  @media only screen and (min-width: $R1366) and (max-width: ($R1600 - 0.02px)) {
    @content;
  }
}

@mixin BPR1366 {
  @media only screen and (min-width: $R1280) and (max-width: ($R1366 - 0.02px)) {
    @content;
  }
}

@mixin BPR1280 {
  @media only screen and (min-width: $R1024) and (max-width: ($R1280 - 0.02px)) {
    @content;
  }
}

@mixin BPR1024 {
  @media only screen and (min-width: $R768) and (max-width: ($R1024 - 0.02px)) {
    @content;
  }
}

@mixin BPR768 {
  @media only screen and (min-width: $R576) and (max-width: ($R768 - 0.02px)) {
    @content;
  }
}

@mixin BPR576 {
  @media only screen and (max-width: ($R576 - 0.02px)) {
    @content;
  }
}
