.riskModal {
  display: flex;
  flex-direction: column;

  .sideModalBody {
    padding: 16px 0;
  }
  .riskModalAddress {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    padding: 0 16px;
    width: 100%;
    border-bottom: 1px solid #EFEFEF;

    .riskModalRow {
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      width: 100%;

      .riskModalCol {
        display: flex;
        flex-direction: column;
        width: 100%;

        b {
          color: #464646;
          font-size: 14px;
          font-weight: bold;
          line-height: 16px;
        }
        .riskModalRisk {
          display: flex;
          align-items: center;

          .icon {
            margin-right: 4px;

            &.green svg path {
              fill: #18AA6E;
            }
            &.yellow svg path {
              fill: #EACF45;
            }
            &.red svg path {
              fill: #FF5959;
            }
          }
          span {
            color: #464646;
            font-size: 14px;
            font-weight: bold;
            line-height: 16px;
          }
        }
        .riskModalColLabel {
          margin-top: 4px;
          color: #9FA3A6;
          font-size: 10px;
          line-height: 12px;
        }
      }
    }
    .riskModalCheck {
      margin: 8px 0;
    }
    .riskModalCheckDate {
      margin-bottom: 16px;
      width: 100%;
      color: #9FA3A6;
      font-size: 10px;
      line-height: 12px;
    }
    .riskModalQuarantine {
      margin-bottom: 16px;
    }
  }
  .riskModalSourcesHead {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    padding: 0 16px;
    width: 100%;

    h3 {
      color: #464646;
      font-size: 14px;
      font-weight: bold;
      line-height: 16px;
    }
    .riskModalSourcesHeadContainer {
      display: flex;
      align-items: center;

      .icon {
        cursor: pointer;

        &:first-child {
          margin-right: 8px;
        }
        &:hover svg path,
        &.active svg path {
          fill: #0D62B5;
        }
        svg path {
          transition: all 0.3s ease-in-out;
          fill: #9FA3A6;
        }
      }
    }
  }
  .riskModalSources {
    display: flex;
    flex-direction: column;
    padding: 0 16px;
    width: 100%;
    overflow: auto;

    .riskModalSource {
      display: flex;
      flex-direction: column;
      margin-bottom: 32px;
      width: 100%;

      &:last-child {
        margin-bottom: 0;
      }
      &.riskModalSourceLow {
        .riskModalSourceTitle .riskIcon svg path {
          fill: #0B8D57;
        }
        .riskModalSourceItem p i {
          background: #0B8D57;
        }
      }
      &.riskModalSourceMedium {
        .riskModalSourceTitle .riskIcon svg path {
          fill: #F5D94A;
        }
        .riskModalSourceItem p i {
          background: #F5D94A;
        }
      }
      &.riskModalSourceHigh {
        .riskModalSourceTitle .riskIcon svg path {
          fill: #FF5959;
        }
        .riskModalSourceItem p i {
          background: #FF5959;
        }
      }
      &.riskModalSourceUncategorized {
        .riskModalSourceTitle .riskIcon svg path {
          fill: #9FA3A6;
        }
        .riskModalSourceItem p i {
          background: #9FA3A6;
        }
      }
      .riskModalSourceTitle {
        display: flex;
        align-items: center;
        margin-bottom: 16px;

        .icon {
          margin-right: 8px;
        }
        span {
          color: #464646;
          font-size: 14px;
          line-height: 16px;
          display: flex;
        }
      }
      .riskModalSourceItem {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 8px;

        p {
          display: flex;
          align-items: center;
          color: #9FA3A6;
          font-size: 10px;
          line-height: 12px;

          i {
            margin-right: 4px;
            width: 8px;
            height: 8px;
            border-radius: 8px;
          }
        }
        span {
          color: #464646;
          font-size: 10px;
          line-height: 12px;
        }
      }
      .riskModalSourceDelimiter {
        margin-bottom: 8px;
        width: 100%;
        height: 1px;
        background: #EFEFEF;
      }
      .riskModalTotal {
        display: flex;
        justify-content: space-between;
        align-items: center;

        p {
          color: #9FA3A6;
          font-size: 10px;
          line-height: 12px;
        }
        b {
          color: #464646;
          font-size: 10px;
          font-weight: bold;
          line-height: 12px;
        }
      }
    }
  }
}
