@import '../../assets/mixins/colors';

.profileModalTabProfile {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  h3 {
    margin-bottom: 4px;
    color: $black;
    font-size: 24px;
    font-weight: 900;
    line-height: 28px;
  }
  .profileModalTabProfileIp {
    margin-bottom: 16px;

    span {
      color: $darkGray;
      font-size: 12px;
      line-height: 14px;
    }
  }
  .profileModalTabProfileBalance {
    display: flex;
    flex-direction: column;

    .profileModalTabProfileBalanceHead {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;

      h4 {
        color: $black;
        font-size: 18px;
        font-weight: bold;
        line-height: 21px;
      }
      span {
        padding: 2px 16px;
        border-radius: 4px;
        font-size: 14px;
        font-weight: 500;
        line-height: 16px;

        &.active {
          background: $lightGreen;
          color: $green;
        }
        &.inactive {
          background: $lightRed;
          color: $red;
        }
      }
    }
    .profileModalTabProfileBalanceBody {
      // margin-bottom: 16px;
      padding: 8px;
      background: $lightGray;
      border-radius: 4px;

      .tooltip {
        margin-bottom: 8px;

        span {
          color: $black;
          font-size: 14px;
          line-height: 16px;
        }
      }
      p {
        color: $black;
        font-size: 14px;
        font-weight: 900;
        line-height: 16px;
      }
    }
  }
  .profileModalTabProfileSeparator {
    margin: 16px 0;
    width: 100%;
    height: 1px;
    background: $gray;
  }
  .profileModalTabProfilePlan {
    padding: 8px;
    background: $lightGray;
    border-radius: 4px;

    h5 {
      display: inline-block;
      margin-bottom: 16px;
      color: $black;
      font-size: 14px;
      font-weight: bold;
      line-height: 16px;

      &:last-child {
        margin-top: 8px;
      }
    }
    .profileModalTabProfileRow {
      display: flex;
      flex-direction: column;
      margin-bottom: 8px;
      padding-bottom: 8px;
      border-bottom: 1px solid $gray;

      &:last-child {
        margin-bottom: 0;
        border-bottom: none;
      }
      & > div:first-child {
        margin-bottom: 8px;
      }
      & > div:last-child {
        display: flex;
        justify-content: space-between;
        align-items: center;

        span {
          color: $darkGray;
          font-size: 14px;
          line-height: 16px;
        }
      }
      p {
        color: $black;
        font-size: 14px;
        font-weight: 900;
        line-height: 16px;
      }
    }
    .profileModalTabProfileRowCurrency {
      margin-bottom: 16px;
    }
  }

  .profileModalTabLogout {
    margin-top: 36px;
  }

  .profileModalTabLogoutBtn {
    background: transparent;
    color: #9fa3a6;
    width: auto;
    margin: auto;

    i svg > path {
      fill: #9fa3a6;
      stroke: #9fa3a6;
    }

    &:hover {
      box-shadow: none;
      color: #0455A3;
      text-decoration: underline;

      i svg > path {
        fill: #0455A3;
        stroke: #0455A3;
      }
    }
  }
}
