.ConfirmationComment {
  margin-bottom: 12px;
  padding: 12px 0 16px 0;
  border-bottom: 1px solid #EFEFEF;

  .ConfirmationComment__Label {
    padding-bottom: 4px;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;
  }

  .ConfirmationComment__Comment {
    overflow: auto;
    min-height: 24px;
    max-height: 30px;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #464646;
  }
}