@import '../../../../../../assets/mixins/colors.scss';

.ReceivingAddressesSelect {
  position: relative;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  padding: 0 18px 0 12px;
  background: $lightGray;
  border: 1px solid $gray;
  box-sizing: border-box;
  border-radius: 4px;

  .click-zone {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: transparent;
    &:hover {
      cursor: pointer;
    }
  }

  &__title {
    position: absolute;
    top: 0;
    left: 8px;
    padding: 0 4px;
    transform: translate3d(0, -9px, 0);
    background: linear-gradient(transparent 50%, $lightGray 50%);
    color: rgba(156, 163, 166, 0.6);
    font-size: 12px;
    line-height: 16px;
    transition: all 0.3s ease-in-out;
  }

  &__placeholder {
    width: 100%;
    height: auto;
    font-size: 14px;
    line-height: 16px;
  }

  &__value,
  .placeholder {
    width: 100%;
    height: auto;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .placeholder {
    color: $darkGray;
  }

  .dropdown-icon {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate3d(-10px, -50%, 0);
  }

  &__dropdown {
    position: absolute;
    width: 100%;
    height: auto;
    top: calc(100% + 1px);
    left: 0;
    z-index: 30;
    background: #FFFFFF;
    border-radius: 4px 4px 0 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }

  .dropdown {
    &__search {
      padding: 16px 14px;
      width: 100%;
      height: 88px;
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid #EFEFEF;
    }

    &__search-title {
      margin-bottom: 8px;
      font-size: 12px;
      line-height: 16px;
      color: $black;
    }

    &__list {
      padding: 0;
      max-height: 200px;
      overflow-y: auto;

      &::-webkit-scrollbar-thumb {
        background-color: $darkGray;
      }
    }

    &__item {
      width: 100%;
      height: auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-grow: 1;
      font-size: 14px;
      line-height: 16px;
      color: $black;
    }

    &__item-name,
    &__item-value {
      height: auto;
      display: flex;
      align-items: center;
    }
    
    &__item-name {
      color: $darkGray;
    }
    
    &__item-value {
      min-width: 54px;

      .icon {
        margin-right: 4px;

        &.gray svg path {
          fill: #DADADA;
        }
        &.green svg path {
          fill: #18AA6E;
        }
        &.yellow svg path {
          fill: #EACF45;
        }
        &.red svg path {
          fill: #FF5959;
        }
      }

      span {
        padding-left: 4px;
      }
    }

    .MuiListItemButton-root {
      height: 40px;
      border-bottom: 1px solid #EFEFEF;
  
      &.active {
        background-color: #EBF5FF;
      }
    }
  }
}