.TableUpload {
  padding-right: 6px;
  width: 32px;
  height: 32px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  .TableUpload__Switcher {
    &.active {
      svg {
        path {
          fill: #0D62B5;
        }
      }
    }
  }

  .TableUpload__Dropdown {
    padding: 4px;
    position: absolute;
    width: 80px;
    height: auto;
    display: flex;
    flex-direction: column;
    top: 100%;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    z-index: 1000;
    background-color: #0455A3;
    border-radius: 4px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.16);

    .Dropdown__Button {
      width: 100%;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      background-color: transparent;
      border-radius: 0;

      &:hover {
        background-color: rgba(255, 255, 255, 0.2);
      }

      .Button__Title {
        font-size: 14px;
        font-weight: 500;
        color: #FFFFFF;
      }
    }
  }
}