@import '../../assets/mixins/colors';

button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  border: 1px solid transparent;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  text-align: center;
  transition: all 0.3s ease-in-out;
  outline: none;
  cursor: pointer;

  &.small {
    height: 32px;
  }

  i {
    margin-right: 12px;

    svg path {
      fill: $white;
      stroke: $white;
      transition: all 0.3s ease-in-out;
    }
  }
}
button.default {
  background: $blue;
  color: $white;

  &:hover {
    box-shadow: $shadow;
  }
  &:focus {
    opacity: 0.6;
  }
  &:disabled, &:disabled:hover, &:disabled:focus {
    box-shadow: none;
    opacity: 0.6;
  }
}
button.light {
  background: $lightBlue;
  border: 1px solid $blue;
  color: $blue;

  i {
    svg path {
      fill: $blue;
      stroke: $blue;
    }
  }

  &:hover {
    background: $blue;
    color: $white;
    box-shadow: $shadow;
    i {
      svg path {
        fill: $white;
        stroke: $white;
      }
    }
  }
  &:focus {
    opacity: 0.6;
  }
  &:disabled, &:disabled:hover, &:disabled:focus {
    background: $lightBlue;
    color: $blue;
    box-shadow: none;
    opacity: 0.6;
  }
}
button.red {
  background: $lightRed;
  border: 1px solid $darkRed;
  color: $darkRed;

  &:hover {
    background: $darkRed;
    border: 1px solid $red;
    color: $white;
    box-shadow: $shadow;
  }
  &:focus {
    opacity: 0.6;
  }
  &:disabled, &:disabled:hover, &:disabled:focus {
    background: $lightRed;
    border: 1px solid $darkRed;
    color: $darkRed;
    box-shadow: none;
    opacity: 0.6;
  }
}
button.green {
  background: $green;
  border: 1px solid $green;
  color: #FFFFFF;

  &:hover {
    background: $green;
    border: 1px solid $green;
    color: $white;
    box-shadow: $shadow;
  }
  &:focus {
    opacity: 0.6;
  }
  &:disabled, &:disabled:hover, &:disabled:focus {
    background: $green;
    border: 1px solid $green;
    color: #FFFFFF;
    box-shadow: none;
    opacity: 0.6;
  }
}
button.warning {
  background: $lightOrange;
  border: 1px solid $orange;
  color: $orange;

  i {
    svg path {
      fill: $orange;
      stroke: $orange;
    }
  }

  &:hover {
    background: $orange;
    color: $white;
    box-shadow: $shadow;
    i {
      svg path {
        fill: $white;
        stroke: $white;
      }
    }
  }
  &:focus {
    opacity: 0.6;
  }
  &:disabled, &:disabled:hover, &:disabled:focus {
    background: $lightOrange;
    color: $orange;
    box-shadow: none;
    opacity: 0.6;
  }
}
