@import '../../assets/mixins/colors';

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  .loginLogo {
    position: absolute;
    top: 72px;

    svg path {
      fill: #0D62B5;
    }
  }
  .loginForm, .loginNew, .loginTwoFactor {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 512px;

    .loginTitle {
      margin-bottom: 32px;
      color: $black;
      font-size: 26px;
      font-weight: 700;
      line-height: 32px;
      text-align: center;
    }
    .loginDescription {
      color: $darkGray;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
    }
    .input, button {
      margin-bottom: 16px;
      width: 100%;
      max-width: 336px;
    }
    .forgotPassword {
      font-weight: bold;
      text-decoration: unset;
      font-size: 14px;
      line-height: 17px;
      color: #0455A3;
      transition: all 300ms ease-out;

      &:hover {
        color: #0D62B5;
        text-decoration: underline;
      }

      &:active {
        opacity: .6;
        text-decoration: unset;
      }
    }
  }
  .loginForm__footer {
    display: flex;
    justify-content: space-between;
    width: 336px;
  }
  .loginNew {
    padding: 98px 8px;
    max-width: 688px;

    i {
      margin-bottom: 24px;
    }
    .loginTitle {
      margin-bottom: 16px;
    }
  }
  .loginTwoFactor {
    max-width: 688px;

    .loginTitle {
      margin-bottom: 16px;
    }
    .loginDescription {
      margin-bottom: 32px;
    }
    .loginTwoFactorInputContainer {
      display: flex;
      justify-content: space-between;
      margin-bottom: 24px;
      width: 400px;

      .input {
        width: 48px;
        height: 48px;

        input {
          width: 100%;
          height: 100%;
          text-align: center;
        }
      }
    }
    .loginTwoFactorSupport {
      width: 100%;
      max-width: 438px;
      color: $black;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
    }
  }

  .auth-domain {
    margin-bottom: 16px;
    width: 100%;
    max-width: 336px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #EFEFEF;

    .MuiFormControlLabel-root {
      margin-left: 0;
      width: 100%;
      max-width: 336px;
    }

    .MuiFormControlLabel-label {
      font: 400 14px/16px'Roboto';
    }

    .input {
      margin-bottom: 4px;
    }
  }
}
