.checkbox {
  position: relative;

  input {
    display: none;
  }

  input:checked + label {
    div {
      background: #0455A3;

      svg {
        visibility: visible;
      }
    }
    span {
      opacity: 1;
    }
  }

  label {
    display: flex;
    align-items: center;

    div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 16px;
      height: 16px;
      border: 2px solid rgba(4, 85, 163, 0.6);
      border-radius: 4px;
      cursor: pointer;

      svg {
        visibility: hidden;
      }
    }
    span {
      color: #464646;
      font-size: 14px;
      line-height: 16px;
      opacity: 0.5;
      margin-left: 4px;
    }
  }

  .checkbox-tooltip {
    position: absolute;
    left: 100%;
    top: 0;
    z-index: 33;
    transform: translate3d(4px, 10px, 0);
  }
}