@import '../../assets/mixins/colors';

.twoFaModal {
  .modal .modalContainer {
    max-width: 688px;

    .twoFaModalError {
      margin-top: -24px;
      margin-bottom: 8px;
      color: $red;
      font-size: 14px;
      line-height: 16px;
    }
    .twoFaModalContainer {
      display: flex;
      align-items: center;
      width: 100%;
      max-width: 452px;

      .input {
        margin-right: 24px;

        &:last-child {
          margin-right: 0;
        }
        input {
          width: 48px;
          min-height: 48px;
          text-align: center;
        }
      }
    }
  }
}
