.AutoScalingInput {
  .hide {
    position: absolute;
    opacity: 0;
    z-index: -100;
    white-space: pre;
  }

  .auto-scale-input {
    border: none;
    padding: 0;
    min-width: 60px;
    text-align: center;
    font-weight: 400;
    font-size: 100%;
    line-height: 24px;

    &:focus {
      outline: none;
    }

    &.underline {
      background-color: #F5F7FB;
    }
  }
}
