@keyframes pulse {
  0% {
    transform: scale(1) rotate(359deg);
  }

  50% {
    transform: scale(1.25) rotate(0);
  }

  100% {
    transform: scale(1) rotate(0);
  }
}

.DataLoader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1050;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 8px;

  .DataLoader__Icon {
    animation: pulse 2s infinite linear reverse;
  }
}