@import '../../assets/mixins/colors';

.menuContainer {
  display: flex;
  flex-direction: column;
  background: $background;
  width: 100vw;

  .menuTop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;
    height: 56px;
    background: #0455A3;

    .menuTopLeft {
      display: flex;
      align-items: center;
      text-decoration: none;
      cursor: pointer;
        
      &:focus {
        opacity: 1;
      }

      i {
        margin-right: 8px;
      }
      p {
        display: flex;
        align-items: center;
        padding: 12px 16px;
        color: #FFFFFF;
        font-size: 14px;
        line-height: 16px;
        transition: all 0.3s ease-in-out;

        b {
          margin-right: 8px;
          padding-right: 8px;
          height: 16px;
          border-right: 1px solid #FFFFFF;
          font-weight: 500;
        }
        span {
          font-weight: 100;
        }
      }
      
      &:hover p, p.active {
        background: rgba(255, 255, 255, 0.08);
        border-radius: 4px;
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.16);
      }
    }
    .menuTopCenter {
      display: flex;
      align-items: center;
      list-style-type: none;

      li {
        margin: 0 8px;
        transition: all 0.3s ease-in-out;
        cursor: pointer;

        a {
          padding: 12px 16px;
          color: #FFFFFF;
          font-size: 14px;
          font-weight: 500;
          line-height: 16px;
          text-decoration: none;

          &:hover, &.active {
            background: rgba(255, 255, 255, 0.08);
            border-radius: 4px;
            box-shadow: 0 0 6px rgba(0, 0, 0, 0.16);
          }

          &:focus {
            opacity: 1;
          }
        }
      }
    }
    .menuTopRight {
      display: flex;
      align-items: center;
      list-style-type: none;

      .menuTopRightItem {
        position: relative;
        margin: 0 4px;
        padding: 4px;
        transition: all 0.3s ease-in-out;
        cursor: pointer;
        user-select: none;

        &:hover, &.active {
          background: rgba(255, 255, 255, 0.08);
          border-radius: 4px;
          box-shadow: 0 0 6px rgba(0, 0, 0, 0.16);
        }
        svg path {
          fill: #FFFFFF;
        }
      }
    }
  }
  .menuBottom {
    padding: 16px 24px;
    width: 100%;
    height: calc(100vh - 56px);
    overflow: auto;
  }
}
