.checkModal {
  .modal {
    z-index: 9999;
  }
  .modal .modalContainer {
    width: 100%;
    max-width: 512px;

    .modalContainerBody {
      padding: 0 56px 56px 56px;

      .checkModalTabs {
        display: flex;
        align-items: center;
        margin-top: -16px;
        margin-bottom: 32px;
        width: 100%;
        list-style-type: none;
        z-index: 3;

        li {
          padding: 8px 12px;
          width: 100%;
          background: #FFFFFF;
          border: 1px solid #9FA3A6;
          color: #9FA3A6;
          font-size: 14px;
          font-weight: bold;
          line-height: 16px;
          text-align: center;
          transition: all 0.3s ease-in-out;
          cursor: pointer;

          &:first-child {
            border-radius: 4px 0 0 4px;
          }
          &:last-child {
            border-radius: 0 4px 4px 0;
          }
          &.active, &:hover {
            background: rgba(4, 85, 163, 0.1);
            border: 1px solid #0455A3;
            color: #0455A3;
          }
        }
      }
      .checkModalInput {
        position: relative;
        width: 100%;

        .checkModalInputRisk {
          position: absolute;
          top: 1px;
          right: 12px;
          display: flex;
          align-items: center;
          padding-left: 8px;
          height: calc(100% - 2px);
          background: #FFFFFF;

          i {
            margin-top: -1px;
            margin-right: 4px;

            &.low svg path {
              fill: #18AA6E;
            }
            &.middle svg path {
              fill: #F5D94A;
            }
            &.high svg path {
              fill: #FF5959;
            }
          }
          span {
            color: #464646;
            font-size: 14px;
            line-height: 16px;
          }
        }
      }
      .checkModalMulti {
        display: flex;
        flex-direction: column;
        width: 100%;

        p {
          margin-bottom: 8px;
          width: 100%;
          color: #9FA3A6;
          font-size: 18px;
          line-height: 24px;
          text-align: center;

          span {
            color: #464646;
            font-weight: bold;
          }
        }
        .checkModalMultiSelect {
          width: 100%;
          height: 280px;
          background: #FFFFFF;
          border: 1px solid #F0F0F0;
          border-radius: 3px;
          overflow: auto;

          .checkModalMultiSelectMerchant {
            margin: 0 8px;
            padding: 8px 0;
            border-bottom: 1px solid #EFEFEF;

            &:last-child {
              border-bottom: none;
            }
            .checkModalMultiSelectMerchantContainer {
              display: flex;
              align-items: center;
              margin-bottom: 12px;

              span {
                margin-right: 4px;
              }
              b {
                color: #464646;
                font-size: 14px;
                font-weight: bold;
                line-height: 16px;
              }
            }
            .checkModalMultiSelectMerchantAddresses {
              padding-left: 16px;

              .checkModalMultiSelectMerchantAddressesAddress {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 12px;

                &:last-child {
                  margin-bottom: 0;
                }
                .checkModalMultiSelectMerchantAddressesAddressLeft {
                  display: flex;
                  align-items: center;

                  div {
                    display: flex;
                    flex-direction: column;

                    b {
                      margin-bottom: 2px;
                      color: #464646;
                      font-size: 14px;
                      font-weight: bold;
                      line-height: 16px;
                    }
                    span {
                      color: #9FA3A6;
                      font-size: 12px;
                      line-height: 14px;
                    }
                  }
                }
                .checkModalMultiSelectMerchantAddressesAddressRight {
                  display: flex;
                  align-items: center;

                  i {
                    margin-top: -1px;
                    margin-right: 4px;

                    &.low svg path {
                      fill: #18AA6E;
                    }
                    &.middle svg path {
                      fill: #F5D94A;
                    }
                    &.high svg path {
                      fill: #FF5959;
                    }
                  }
                  span {
                    color: #464646;
                    font-size: 14px;
                    line-height: 16px;
                  }
                }
              }
            }
          }
        }
      }
      .checkModalDescription {
        margin: 32px 0 16px 0;
        width: 100%;
        color: #9FA3A6;
        font-size: 18px;
        line-height: 24px;
        text-align: center;

        span {
          color: #464646;
          font-weight: bold;
        }
      }
      .checkModalError {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 32px 0 16px 0;
        width: 100%;

        i {
          margin-top: -1px;
          margin-right: 12px;
        }
        span {
          color: #FF6161;
          font-size: 18px;
          line-height: 24px;
        }
      }
      .checkModalMessage {
        margin: 16px 0;
        padding: 12px;
        width: 100%;
        background: rgba(4, 85, 163, 0.1);
        border-radius: 4px;
        color: #464646;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
      }
    }
  }
}
