@import '../../assets/mixins/colors';

.funds {
  display: flex;
  flex-direction: column;
  height: 100%;

  .fundsHead {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;

    .fundsHeadButtons {
      display: flex;
      align-items: center;

      button {
        margin-left: 16px;
        height: 40px;

        &:first-child {
          margin-left: 0;
        }
        //&:last-child {
        //  display: flex;
        //  justify-content: center;
        //  align-items: center;
        //  padding: 0;
        //  width: 40px;
        //  height: 40px;
        //
        //  i {
        //    margin-right: 0;
        //  }
        //}
        &.fundsHeadButtonsCheck {
          width: 218px;
          letter-spacing: -0.3px;
        }
        &.fundsHeadButtonsSend,
        &.fundsHeadButtonsReceive {
          width: 126px;
        }
        &.fundsHeadButtonsAutoWithdrawal {
          width: 200px;
        }
      }
    }
  }
}
