.AmountInputs {
  margin-bottom: 20px;

  .AmountInputs__Body {
    width: 100%;
    height: auto;
  }

  .AmountInputs__Title {
    margin-bottom: 15px;
    text-align: center;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;

    &.low-balance {
      color: #FF6161;
    }
  }

  .currency {
    margin: 0 4px;
    font-size: 12px;
    line-height: 22px;
    color: #9FA3A6;
  }

  .field-group {
    position: relative;

    .SwitchIcon {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate3d(0, -50%, 0);

      &:hover {
        cursor: pointer;
      }
    }
  }

  .input-field {
    margin-bottom: 4px;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    border-bottom: 1px solid #EFEFEF;
    overflow-x: hidden;
    padding-bottom: 4px;
  }

  .button-max {
    position: absolute;
    right: 32px;
    background-color: #F5F7FB;
    border-radius: 8px;
    padding: 4px;
    font-size: 12px;
    line-height: 16px;
    color: #0455A3;
    cursor: pointer;
  }

  .converter {
    position: relative;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    overflow: hidden;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;

    .currency {
      font-size: 14px;
      line-height: 16px;
    }

  }
}