@import '../../assets/mixins/colors';

.profileModalTabSettings {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  .d-none {
    display: none;
  }

  .profileModalTabSettingsSeparator {
    margin: 16px 0;
    width: 100%;
    min-height: 1px;
    background: #EFEFEF;
  }
  .profileModalTabSettingsInfo,
  .profileModalTabSettingsSecurity,
  .profileModalTabSettingsTwoFa,
  .profileModalTabSettingsNotification,
  .profileModalTabSettingsTimezone,
  .profileModalTabSettingsLanguage,
  .profileModalTabSettingsOther,
  .profileModalTabSettingsBackup,
  .profileModalTabSettingsGasStation {
    display: flex;
    flex-direction: column;

    h4 {
      display: flex;
      align-items: center;
      margin-bottom: 24px;

      & > i {
        margin-top: -4px;
      }
      & > span {
        margin-left: 8px;
        color: #464646;
        font-size: 18px;
        font-weight: bold;
        line-height: 21px;
      }
    }
    p {
      min-height: 50px;
      margin-bottom: 16px;
      color: #9FA3A6;
      font-size: 14px;
      line-height: 16px;
    }
    .input {
      margin-bottom: 16px;
    }
    .checkbox {
      margin-bottom: 8px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .profileModalTabSettingsInfo {
    .profileModalTabSettingsInfoButtons {
      display: flex;
      justify-content: space-between;
      align-items: center;

      button:first-child {
        margin-right: 4px;
      }
      button:last-child {
        margin-left: 4px;
      }
    }
  }
  .profileModalTabSettingsTwoFa {
    h4 {
      margin-bottom: 8px;
    }
  }
  .profileModalTabSettingsNotification {
    .profileModalTabSettingsNotificationHead {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;

      h4 {
        margin-bottom: 0;
      }
    }
  }
  .profileModalTabSettingsOther {
    button {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
