@import '../../../assets/mixins/colors';

.fundsMain {
  height: calc(100% - 48px);
  display: flex;
  flex-direction: column;

  .fundsMainCurrencies {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;

    h4 {
      margin-bottom: 16px;
      color: #464646;
      font-size: 14px;
      font-weight: bold;
      line-height: 16px;
    }
  }
  .fundsMainTables {
    max-height: 100%;
    display: flex;
    flex-direction: column;

    .fundsMainTablesMenu {
      display: flex;
      list-style-type: none;
      margin-bottom: 16px;

      li {
        margin-right: 32px;
        color: $darkGray;
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;
        transition: all 0.3s ease-in-out;
        cursor: pointer;

        &:last-child {
          margin-right: 0;
        }
        &.active, &:hover {
          color: $black;
        }
      }
    }
  }
}
