.inputFile {
  width: 100%;

  input {
    display: none;
  }
  label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
    width: 100%;
    height: 40px;
    background: #FFFFFF;
    border: 1px solid #E6E6E6;
    border-radius: 4px;
    cursor: pointer;

    span {
      width: 80%;
      height: 16px;
      color: #9FA3A6;
      font-size: 14px;
      line-height: 16px;
      word-break: break-all;
      overflow: hidden;
    }
    i svg path {
      fill-opacity: 1;
    }
  }
}