@import '../../assets/mixins/colors';

.input {
  position: relative;
  width: 100%;

  input {
    padding: 12px;
    width: 100%;
    height: 40px;
    background: $white;
    border: 1px solid $gray;
    border-radius: 4px;
    color: $black;
    font-size: 14px;
    line-height: 16px;
    outline: none;
    -webkit-box-shadow: inset 0 0 0 50px $white;
    -webkit-text-fill-color: $black;
    transition: all 0.3s ease-in-out;

    &::placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
      opacity: 1;
    }
    &:-ms-input-placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
    }
    &::-ms-input-placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
    }
    &:focus {
      border: 1px solid $blue;
    }
    &:disabled {
      background: $lightGray;
      -webkit-box-shadow: inset 0 0 0 50px $lightGray;

      & + label {
        background: linear-gradient(transparent 50%, #F5F7FB 50%);
      }
    }
  }
  label {
    position: absolute;
    top: -8px;
    left: 8px;
    padding: 0 4px;
    background: $white;
    color: $darkGray;
    font-size: 12px;
    line-height: 16px;
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }
  .inputPasswordEye {
    position: absolute;
    top: 12px;
    right: 12px;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &:hover svg path,
    &.inputPasswordEyeActive svg path {
      fill: #0D62B5;
    }
    svg path {
      fill: #9FA3A6;
    }
  }
  input:not(:placeholder-shown) + label {
    opacity: 1;
  }
  input:focus + label {
    color: $blue;
    opacity: 1;
  }
  &.inputError {
    input {
      border: 1px solid $darkRed;
      color: $darkRed;
      -webkit-text-fill-color: $darkRed;
    }
    label {
      color: $darkRed !important;
    }
  }

  .search-icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate3d(6px, -50%, 0);
  }
  &.search {
    input {
      height: 32px;
      padding: 8px 12px 8px 26px;
    }
  }

  .input-tooltip {
    position: absolute;
    left: 100%;
    top: 0;
    z-index: 33;
    transform: translate3d(4px, 10px, 0);
  }
}
