.AdditionalDropdown {

  &.open {
    .toggle-button-icon {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  .AdditionalDropdown__Button {
    background-color: transparent;
  }

  .toggle-button {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;
    background-color: transparent;
  }

  .toggle-button-text {
    position: relative;
  }

  .toggle-button-icon {
    position: absolute;
    top: 0;
    left: 100%;
    transform: translate3d(0, -2px, 0);

    svg {
      transform: rotate(0);
      transition: transform 0.35s linear;

      path {
        fill: #9FA3A6;
      }
    }
  }

  .AdditionalDropdown__Body {
    padding-top: 16px;

    .fee {
      font-weight: 700;
      margin-bottom: 16px;
      font-size: 14px;
      line-height: 16px;
    }
  }
}