@import '../../assets/mixins/colors';

.forgot {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  .forgotLogo {
    position: absolute;
    top: 72px;

    svg path {
      fill: #0D62B5;
    }
  }
  .forgotForm, .forgotMail, .forgotUpdateForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 512px;

    .forgotTitle {
      margin-bottom: 16px;
      color: $black;
      font-size: 26px;
      font-weight: bold;
      line-height: 32px;
      text-align: center;
    }
    .forgotDescription {
      margin-bottom: 32px;
      width: 100%;
      max-width: 512px;
      color: $darkGray;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
    }
    .input, button {
      margin-bottom: 16px;
      width: 100%;
      max-width: 336px;
    }

    .returnLink {
      font-weight: bold;
      text-decoration: unset;
      font-size: 14px;
      line-height: 17px;
      color: #0455A3;
      transition: all 300ms ease-out;

      &:hover {
        color: #0D62B5;
        text-decoration: underline;
      }

      &:active {
        opacity: .6;
        text-decoration: unset;
      }
    }
  }
  .forgotMail {
    padding: 98px 8px;

    i {
      margin-bottom: 24px;
    }
    .forgotCodeInputContainer {
      display: flex;
      justify-content: space-between;
      margin-bottom: 24px;
      width: 400px;

      .input {
        width: 48px;
        height: 48px;

        input {
          width: 100%;
          height: 100%;
          text-align: center;
        }
      }
    }
  }
}
