.importantModal {
  .modal .modalContainer {
    max-width: 688px;

    .modalContainerBody {
      .inputFile, .progress {
        margin-bottom: 16px;
        width: 100%;
        max-width: 336px;
      }
      p {
        margin-bottom: 32px;
        width: 100%;
        max-width: 336px;
        color: #464646;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
      }
      button {
        width: 100%;
        max-width: 336px;
      }
    }
  }
}