@import '../../assets/mixins/colors';

.background {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw !important;
  height: 100vh !important;
  overflow: hidden;
  z-index: -1;

  svg {
    width: 100vw;
    height: auto;

    @keyframes spin {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    @keyframes spinBack {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(-360deg);
      }
    }
    path {
      transform-origin: center;
    }
    #path1 {
      animation: spin 20s 0s linear infinite;
    }
    #path2 {
      animation: spinBack 24s 0s linear infinite;
    }
    #path3 {
      animation: spin 42s 0s linear infinite;
    }
    #path4 {
      animation: spinBack 27s 0s linear infinite;
    }
    #path5 {
      animation: spin 48s 0s linear infinite;
    }
    #path6 {
      animation: spinBack 28s 0s linear infinite;
    }
    #path7 {
      animation: spin 32s 0s linear infinite;
    }
    #path8 {
      animation: spinBack 41s 0s linear infinite;
    }
    #path9 {
      animation: spin 28s 0s linear infinite;
    }
    #path10 {
      animation: spinBack 25s 0s linear infinite;
    }
    #path11 {
      animation: spin 29s 0s linear infinite;
    }
    #path12 {
      animation: spinBack 29s 0s linear infinite;
    }
    #path13 {
      animation: spin 26s 0s linear infinite;
    }
    #path14 {
      animation: spinBack 39s 0s linear infinite;
    }
  }
}
