@import '../../assets/mixins/colors.scss';

.walletsList {
  width: 296px;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  margin-right: 16px;
  padding-top: 8px;
  background: $white;

  button:focus {
    opacity: 1;
  }
}

.searchInput {
  position: relative;
  margin: 8px 16px;

  i {
    position: absolute;
  }

  &__searchIcon {
    top: 4px;
    left: 4px;
  }
  &__clear {
    top: 8px;
    right: 8px;
    cursor: pointer;
  }
  input {
    width: 100%;
    padding: 7px 31px 7px 35px;
    background: $white;
    border: 1px solid $gray;
    border-radius: 4px;
    color: $black;
    font-size: 14px;
    line-height: 16px;
    outline: none;
    -webkit-box-shadow: inset 0 0 0 50px $white;
    -webkit-text-fill-color: $black;
    transition: all 0.3s ease-in-out;

    &::placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
      opacity: 1;
    }
    &:-ms-input-placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
    }
    &::-ms-input-placeholder {
      color: $darkGray;
      -webkit-text-fill-color: $darkGray;
    }
    &:focus {
      border: 1px solid $blue;
    }
    &:disabled {
      background: $lightGray;
      -webkit-box-shadow: inset 0 0 0 50px $lightGray;
    }
  }
  input:not(:placeholder-shown) + label {
    opacity: 1;
  }
  input:focus + label {
    color: $blue;
    opacity: 1;
  }
}

.walletSumBlock {
  position: relative;
  padding: 8px;
  border-top: 1px solid #EFEFEF;
  border-bottom: 1px solid #EFEFEF;
  cursor: pointer;

  &__content {
    display: flex;
    align-items: center;
    padding: 8px;
  }

  &__currency {
    margin-right: 8px;
  }
}

.walletSum {
  &__text {
    margin-bottom: 2px;
    font-size: 14px;
    line-height: 16px;
    color: $darkGray;
  }

  &__number {
    font-size: 24px;
    line-height: 28px;
    font-weight: bold;
    color: #464646;
  }
}

.walletSumBlock.red {
  .walletSum__number {
    color: $red;
  }
}

.walletListItems {
    height: calc(100% - 128px);
    overflow: auto;
    padding-bottom: 8px;
}

.wallet {
  position: relative;
  padding: 8px;
  border-bottom: 1px solid #EFEFEF;
  cursor: pointer;

  &__content {
    padding: 8px;

    .walletInfo {
      display: flex;

      .currency {
        flex: 1;
        display: flex;
    
        &__icon {
          margin-right: 4px;
        }
        
        &__name {
          margin-bottom: 2px;
          font-size: 14px;
          line-height: 16px;
          color: #464646;
        }
    
        &__rate {
          font-size: 12px;
          line-height: 14px;
          color: $darkGray;
        }
      }
    
      .amount {
        flex: 1;
    
        &__currencyAmount {
          margin-bottom: 2px;
          font-size: 14px;
          line-height: 16px;
          font-weight: bold;
          color: #464646;
        }
    
        &__currency {
          font-size: 10px;
          line-height: 12px;
        }
        
        &__fiatAmount {
          font-size: 12px;
          line-height: 14px;
          color: $darkGray;
        }
      }
    }

    .walletControls {
      display: flex;
      margin-top: 8px;
      padding-top: 8px;
      border-top: 1px solid #EFEFEF;

      &__send {
        flex: 1;
      }

      &__receive {
        flex: 1;
        margin-left: 8px;
      }
    }
  }
}

.walletSumBlock,
.wallet {
  &__active {
    cursor: default;

    &::before {
      content: '';
      position: absolute;
      width: 4px;
      height: 31px;
      background: $blue;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 8px 50px 50px 8px;
    }

    .walletSumBlock__content,
    .wallet__content {
      background: rgba(4, 85, 163, 0.05);
      border: 1px solid #EFEFEF;
      border-radius: 8px;
      padding: 7px;
    }

    &.red {
      &::before {
        background: $red;
      }
      .walletSumBlock__content,
      .wallet__content {
        background: rgba(255, 97, 97, 0.1);
        border: 1px solid #EFEFEF;
        border-radius: 8px;
        padding: 7px;
      }
    }
  }
}