@import '../../../../assets/mixins/colors.scss';

#RootModal {
  #Receiving {
    height: auto;

    .Container__Body {
      padding: 8px 88px 56px 88px;
    }

    .Receiving {
      &__settings {
        padding-top: 10px;
      }

      &__additionally {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 36px;
        
        .additionally {
          &__toggle-wrapper {
            margin-bottom: 16px;
          }

          &__toggle {
            height: 16px;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: $darkGray;
            background-color: transparent;

            &.open {
              .dropdown-icon {
                svg {
                  transform: rotate(180deg);
                }
              }
            }
          }

          &__toggle-inside {
            position: relative;
            font-size: 14px;
            line-height: 16px;
            color: $darkGray;

            .dropdown-icon {
              position: absolute;
              top: -4px;
              left: 100%;

              svg {
                transform: rotate(0);
                transition: transform 0.35s linear;

                path {
                  fill: $darkGray;
                }
              }
            }
          }
        }
      }

      &__creating-info {
        margin: 16px 0;
        text-align: center;
        font-size: 14px;
        line-height: 16px;
        color: $darkGray;
      }

      &__merchant-link {
        font-weight: bold;
        color: $blue;
        text-decoration: none;
      }

      .creation-paid {
        width: 100%;
        margin-top: 16px;
        margin-bottom: 32px;

        &__label {
          display: flex;
          margin-bottom: 16px;

          span {
            font-size: 14;
            line-height: 16px;
            font-weight: bold;
            margin-right: 4px;
          }
        }
      }

      &__send {
        min-width: 180px;
      }

      &__send-button {
        text-transform: none;
      }
    }
  }
}