.ConfirmationAddress {
  margin-bottom: 12px;
  padding: 12px 0 16px 0;
  border-bottom: 1px solid #EFEFEF;

  .ConfirmationAddress__Label {
    padding-bottom: 4px;
    font-size: 14px;
    line-height: 16px;
    color: #9FA3A6;
  }

  .ConfirmationAddress__List {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 20px;
    max-height: 100px;
    overflow-y: auto;
    overflow-x: hidden;

    .item {
      width: 100%;
      height: 20px;
      display: flex;
      flex-grow: 1;
      list-style-type: none;

      .address, .risk {
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
      }

      .address {
        width: calc(100% - 30px);
        margin-right: 6px;
        font-size: 12px;
        line-height: 14px;
        color: #0455A3;
      }

      .risk {
        width: 30px;
        justify-content: flex-end;
        align-items: center;
        font-size: 12px;
        line-height: 16px;
        color: #464646;
      }

      .risk-wrapper {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        min-width: 30px;
      }
    }
  }

  .check-icon {
    &:hover {
      cursor: pointer;
    }
    svg {
      path {
        fill: #0B8D57;
      }
    }
  }
}
