.dateFromPicker,
.dateToPicker {
  button.MuiIconButton-sizeSmall {
    width: auto;
    height: auto;
  }

  .cell,
  .empty-cell {
    .day-wrap {
      border: 2px dashed transparent;

      button {
        background-color: transparent;
        &:hover {
          background-color: rgba(0, 0, 0, 0.04);
        }
      }
    }
  }

  div[role="row"] {
    .start,
    .end {
      &:hover button {
        background-color: rgb(21, 101,192);
      }

      button {
        color: white;
        background-color: rgb(25, 118,210);
      }
    }

    .start,
    .afterStart.beforeEnd,
    .end {
      background-color: rgba(66, 165, 245, 0.6);
    }

    .start,
    .weekStart,
    .monthStart {
      border-top-left-radius: 50%;
      border-bottom-left-radius: 50%;
    }

    .end,
    .weekEnd,
    .monthEnd {
      border-top-right-radius: 50%;
      border-bottom-right-radius: 50%;
    }
  }
}

.dateFromPicker {
  div[role="row"] {
    .cell:hover,
    .cell:hover ~ .cell,
    &:hover ~ div[role="row"] .cell {
      &.beforeEnd .day-wrap {
        border-top-color: rgba(0, 0, 0, 0.12);
        border-bottom-color: rgba(0, 0, 0, 0.12);
      }

      &.end .day-wrap {
        border-right-color: rgba(0, 0, 0, 0.12);
        border-top-right-radius: 50%;
        border-bottom-right-radius: 50%;
        border-top-color: rgba(0, 0, 0, 0.12);
        border-bottom-color: rgba(0, 0, 0, 0.12);
      }
    }

    .cell:hover,
    &:hover ~ div[role="row"] .weekStart {
      &.beforeEnd .day-wrap,
      &.end .day-wrap {
        border-left-color: rgba(0, 0, 0, 0.12);
        border-top-left-radius: 50%;
        border-bottom-left-radius: 50%;
      }
    }

    &:hover .cell:hover ~,
    &:hover ~ div[role="row"] {
      .weekEnd,
      .monthEnd {
        &.beforeEnd .day-wrap {
          border-right-color: rgba(0, 0, 0, 0.12);
          border-top-right-radius: 50%;
          border-bottom-right-radius: 50%;
        }
      }
    }

    &:hover {
      .cell.weekEnd:hover,
      .cell.monthEnd:hover {
        .day-wrap {
          border-right-color: rgba(0, 0, 0, 0.12);
          border-top-right-radius: 50%;
          border-bottom-right-radius: 50%;
        }
      }
    }

    .cell:hover.afterStart:not(.beforeEnd) .day-wrap{
      border-color: rgba(0, 0, 0, 0.12);
      border-radius: 50%;
    }
  }
}

.dateToPicker {
  div[role="grid"]:hover {
    div[role="row"] {
      .start,
      .afterStart {
        .day-wrap {
          border-top-color: rgba(0, 0, 0, 0.12);
          border-bottom-color: rgba(0, 0, 0, 0.12);
        }
      }

      .start,
      .weekStart.afterStart ,
      .monthStart.afterStart  {
        .day-wrap {
          border-left-color: rgba(0, 0, 0, 0.12);
          border-top-left-radius: 50%;
          border-bottom-left-radius: 50%;
        }
      }

      .cell:hover,
      .weekEnd.start,
      .weekEnd.afterStart,
      .monthEnd.start,
      .monthEnd.afterStart {
        .day-wrap {
          border-right-color: rgba(0, 0, 0, 0.12);
          border-top-right-radius: 50%;
          border-bottom-right-radius: 50%;
        }
      }

      .cell:hover.beforeEnd:not(.afterStart) .day-wrap{
        border-color: rgba(0, 0, 0, 0.12);
        border-radius: 50%;
      }
      
      .cell:hover ~ .cell,
      &:hover ~ div[role="row"] .cell {
        .day-wrap {
          border-color: transparent;
        }
      }
    }
  }
}