.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;

  .modalContainer {
    width: 100%;
    max-width: 512px;
    background: #FFFFFF;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    .modalContainerHeader {
      position: relative;
      padding: 56px 88px 32px 88px;

      i {
        position: absolute;
        top: 24px;
        right: 26px;
        cursor: pointer;
      }
      h2 {
        color: #303030;
        font-size: 26px;
        font-weight: bold;
        line-height: 32px;
        text-align: center;
      }
      p {
        margin-top: 16px;
        color: #9FA3A6;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
      }
    }
    .modalContainerBody {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 88px 56px 88px;
    }
  }
}