@import '../../assets/mixins/colors';

.administrators {
  height: 100%;
  display: flex;
  flex-direction: column;

  .select.disabled > *:not(label) {
    opacity: 0.5;
  }

  .administratorsModal {
    .modalContainerBody {
      form {
        width: 100%;

        .input, .select {
          margin-bottom: 16px;
        }
      }
    }
  }
  .administratorsHead {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;

    h3 {
      color: $blue;
      font-size: 18px;
      font-weight: bold;
      line-height: 24px;
    }
    .administratorsHeadContainer {
      display: flex;
      align-items: center;

      .administratorsHeadContainerAdd {
        margin-right: 16px;
        height: 40px;
      }
      .administratorsHeadContainerReference {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        width: 40px;
        height: 40px;

        i {
          margin-right: 0;
        }
      }
    }
  }

  .administratorsTable {
    height: calc(100% - 48px);

    .tableWrapper {
      width: 100%;
      height: 100%;
    }
  }
}
