@import '../../assets/mixins/colors';

.filter {
  position: relative;
  z-index: 1;

  .icon {
    margin-top: -2px;
    margin-right: 8px;
    min-width: 16px;
    min-height: 16px;
    cursor: pointer;

    &:hover svg path {
      fill: $blue;
    }
    &.filterReset svg path {
      fill: #FF6161;
    }
    svg path {
      fill: $darkGray;
      transition: all 0.3s ease-in-out;
    }
  }
  .filterSearch {
    position: absolute;
    top: calc(100% + 8px);
    left: 4px;
    padding: 8px 12px;
    width: 220px;
    background: #FFFFFF;
    border-radius: 3px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  .filterNumber {
    position: absolute;
    top: calc(100% + 8px);
    left: 4px;
    display: flex;
    align-items: center;
    padding: 8px 12px;
    width: 220px;
    background: #FFFFFF;
    border-radius: 3px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    .select {
      width: 80px;

      span {
        padding: 12px 8px;
        width: 80px;
        background: transparent;
        border-right: 0;
        border-radius: 4px 0 0 4px;
        font-size: 12px !important;
      }
      .icon {
        margin-top: 0;
        margin-right: 0;
      }
    }
    .input input {
      border-radius: 0 4px 4px 0;
    }
  }
  .filterSelect {
    position: absolute;
    top: calc(100% + 8px);
    left: 4px;
    padding: 8px 12px;
    width: 220px;
    background: #FFFFFF;
    border-radius: 3px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

    .select {
      span {
        background: transparent;
      }

      ul {
        max-height: 295px;
        overflow: auto;
      }
    }
  }
}
