.sideModal {
  position: fixed;
  top: 56px;
  right: -720px;
  display: flex;
  flex-direction: column;
  width: 360px;
  height: calc(100vh - 56px);
  background: #FFFFFF;
  box-shadow: -4px 0 8px rgba(0, 0, 0, 0.16);
  z-index: 99;
  transition: all 0.3s ease-in-out;

  &.sideModalOpen {
    right: 0;
  }
  .sideModalHeader {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0;
    margin: 0 16px;
    border-bottom: 1px solid #EFEFEF;

    i {
      cursor: pointer;
    }
    h2 {
      color: #464646;
      font-size: 18px;
      font-weight: bold;
      line-height: 21px;
    }
  }
  .sideModalBody {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
  }
}