.GasStation {
  padding: 6px 0 16px 0;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  flex-grow: 1;

  .GasStation__Name,
  .GasStation__Details {
    width: 100%;
    height: auto;
    display: flex;
  }

  .Icon {
    svg {
      margin-right: 4px;
      width: 16px;
      height: 16px;
    }
  }

  .Name,
  .Amount {
    font: 400 14px/16.41px 'Roboto', sans-serif;
    color: #464646;
  }

  .Amount {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: flex-end;

    .Value {
      padding-right: 4px;
    }


    .Currency {
      padding-right: 6px;
      font-size: 10px;
      color: #9FA3A6;
    }
  }

  .EditButton {
    &:hover {
      cursor: pointer;
    }
  }
}
