@import '../../assets/mixins/colors';

.PageHeadTabs {
    display: flex;
    list-style-type: none;
    position: relative;
    height: 40px;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      height: 4px;
      background: #e6e6e6;
      width: 100%;
      border-radius: 2px;
    }

    &__tab {
      display: flex;
      align-items: flex-start;
      margin-right: 16px;
      transition: all 0.3s ease-in-out;
      cursor: pointer;

      a, span {
        padding-top: 4px;
        border: 1px solid transparent;
        color: $black;
        font-size: 18px;
        line-height: 24px;
        font-weight: bold;
        text-decoration: none;
        position: relative;

        &:after {
          opacity: 0;
          content: '';
          position: absolute;
          left: 0;
          bottom: -11px;
          height: 4px;
          background: $blue;
          z-index: 1;
          width: 100%;
          border-radius: 2px;
          transition: all .3s ease-in-out;
        }

        &:focus {
          opacity: 1;
        }
      }

      &:hover a, a.active {
        color: $blue;

        &:after {
          opacity: 1;
        }
      }

      &.quarantine-disabled{
        opacity: .3;
        cursor: not-allowed;
      }

      &.no-highLight {
        &:hover a, a.active {
          color: $blue;
          background: none;
          border-color: transparent;
          border-radius: 0;
          padding-right: 4px;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      .tooltip {
        align-self: center;
      }
    }
  }