.Switcher__Body {
  position: relative;
  margin-bottom: 22px;

  .Switcher__Tabs {
    padding: 4px;
    margin-bottom: 8px;
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    background: #FFFFFF;
    border: 1px solid #9FA3A6;
    box-sizing: border-box;
    border-radius: 4px;

    &.disabled {
      background-color: #F5F7FB;
      opacity: .5;
      
      .commission-tab {
        background-color: #F5F7FB;
        cursor: not-allowed;
      }
    }

    .commission-tab {
      font-size: 14px;
      font-weight: 400;
      line-height: 16px;
      height: 32px;
      background-color: #FFFFFF;
      color: #9FA3A6;

      &.active {
        background: #0455A3;
        color: #FFFFFF;
      }
    }
  }

  .switcher-title {
    font-size: 12px;
    text-align: center;
    color: #9FA3A6;
  }

  .switcher-tooltip {
    position: absolute;
    left: 100%;
    top: 0;
    z-index: 34;
    transform: translate3d(4px, 10px, 0);
  }
}