.deleteModal {
  .modalContainerBody {
    p {
      width: 110%;
      color: #9FA3A6;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
    }
    .deleteModalSubtitle {
      margin-top: -16px;
    }
    .deleteModalDescription {
      margin-top: 8px;
      a {
        text-decoration: none;
        cursor: auto;
        font-weight: 600;
        color: #303030;
        &:hover {
          cursor: auto;
          font-weight: 600;
          color: #303030;
        }
      }
    }
    .deleteModalButtons {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 32px;
      width: 100%;

      button {
        &:first-child {
          margin-right: 12px;
        }
        &:last-child {
          margin-left: 12px;
        }
      }
    }
  }
}
