.ConfirmationCurrency {
  margin-bottom: 12px;
  padding: 12px 0 16px 0;
  border-bottom: 1px solid #EFEFEF;

  .ConfirmationCurrency__Labels {
    margin-bottom: 4px;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;

    .label {
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 16px;
      color: #9FA3A6;
    }

    .usd-amount {
      justify-content: flex-end;
    }
  }

  .ConfirmationCurrency__Details {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;

    .detail {
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      font-size: 18px;
      line-height: 24px;
      color: #464646;
    }

    .crypto-currency {
      .currency-icon {
        margin-right: 6px;
        transform: translate3d(0, 2px, 0);
      }
    }

    .crypto-amount {
      justify-content: flex-end;
      align-items: flex-end;

      .currency {
        margin-left: 6px;
        font-size: 12px;
        line-height: 14px;
        color: #9FA3A6;
        transform: translate3d(0, -4px, 0);
      }
    }
  }
}