.mainTabMainConnection {
  &.disable {
    #Vector-302,
    #Vector-306,
    #Vector-307,
    #Vector-317,
    #Vector-318,
    #Vector-319,
    #Vector-320,
    #Vector-321,
    #Vector-322,
    #Vector-323,
    #Vector-324 {
      stroke: #FF6161;
    }

    #Node,
    #Node_2,
    #Node_3,
    #Node_4,
    #Node_5,
    #Node_6,
    #Node_7,
    #Node_8,
    #Lock {
      path {
        fill: #FF6161;
      }
    }

    #Vector-302-1,
    #Vector-306-1,
    #Vector-307-1,
    #Vector-317-1,
    #Vector-318-1,
    #Vector-319-1,
    #Vector-320-1,
    #Vector-321-1,
    #Vector-322-1,
    #Vector-323-1,
    #Vector-324-1
    {
      display: none;
    }
  }
}

#connectionSvg {
  display: block;
  width: 80%;
}

.foreignObject{
    position: relative;
    font-size: 14px;
    font-weight: bold;
    color: #0455A3;
    font-family: Roboto;

    .block-ip {
        position: absolute;
        bottom: 6px;
        width: 100%;
        text-align: center;

        &.node {
            font-size: 12px;
        }
    }
}

#Vector-307-1 {
  stroke-dasharray: 30 230;
  stroke-dashoffset: 30;
  stroke: #18AA6E;
  box-shadow: 0px 0px 8px #18AA6E;
  filter: blur(1px);
  animation: Vector-307-1 .8s linear alternate infinite;
}

@keyframes Vector-307-1 {
  from {
    stroke-dashoffset: 30;
  }
  to {
    stroke-dashoffset: -220;
  }
}

