.riskScoreCell {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .riskScoreCellInfo {
    display: flex;
    flex-direction: column;

    div {
      display: flex;
      align-items: center;

      .icon {
        margin-right: 4px;

        &.gray svg path {
          fill: #DADADA;
        }
        &.green svg path {
          fill: #18AA6E;
        }
        &.yellow svg path {
          fill: #EACF45;
        }
        &.red svg path {
          fill: #FF5959;
        }
      }
      span {
        color: #464646;
        font-size: 14px;
        font-weight: bold;
        line-height: 16px;
      }
    }
    p {
      color: #9FA3A6;
      font-size: 12px;
      line-height: 14px;
    }
  }
  .riskScoreCellActions {
    display: flex;
    align-items: center;
    margin-top: 2px;

    i {
      margin-right: 8px;
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }
      &.riskScoreCellActionsQuarantine svg path {
        fill: #9FA3A6;
      }
      &.riskScoreCellActionsCheck svg path {
        fill: #0B8D57;
      }
      &.riskScoreCellActionsEye.disabled {
        cursor: default;
      }
    }
  }
}